<template>
  <div>
    <slot name="button" :push="push" class="mx-2"></slot>
    <sv-modal v-if="matchedRoute" ref="Modalable" :visible.sync="modalOpen" :size="size">
      <component :is="matchedRoute.component" v-bind="comprops"></component>
    </sv-modal>
  </div>
</template>
<script>
export default {
  name: 'SvModalLink',
  props: ['url'],
  data() {
    return {
      matchedRoute: {},
      modalOpen: false
    }
  },
  computed: {
    size() {
      return 'w-2/3'
    },
    comprops() {
      return { ...this.$props, ...this.matchedRoute.props }
    },
  },
  methods: {
    push() {
      this.matchedRoute = this.$router.match('/' + this.url)
      if (this.matchedRoute) {
        this.$nextTick(() => this.modalOpen = true)
      }
    },
  },
}
</script>
