<template>
  <div>
    <template v-if="presenting">{{ value }}</template>
    <template v-else>
      <the-mask v-if="mask" :placeholder="placeholder" class="sv-input" v-model="input" :mask="mask"></the-mask>
      <input :placeholder="placeholder" class="sv-form-control" v-else v-model="input">
    </template>
  </div>
</template>

<script>
import BaseField from './BaseField.js'
import { TheMask } from 'vue-the-mask'

export default {
  name: 'SvTextField',
  components: { TheMask },
  mixins: [BaseField],
  computed: {
    mask() {
      return this.$meta.mask
    },
  },
}
</script>
