<template>
  <div :style="{'padding-top' : tabsActive ? '29px' : 0 }" class="sv-router-container relative" v-if="ready">
    <div :style="{'left':!sidebarCollapsed ?  (sidebar.width + 'px') : '64px' }"
         class="tabs-nav-container flex items-stretch justify-between"
         v-if="tabsActive">

      <ul class="tabs-nav flex items-stretch">
        <li :class="[tab.active ? 'active' : '']"
            :key="tab.id"
            class="tabs-nav-item flex justify-between items-center mx-2 pl-2"
            v-for="tab in model.tabs"
        >
          <a @click.prevent="model.activateTab(tab)" @dblclick.alt="model.duplicateTab(tab)"
             class="tabs-nav-link">
            <p class="tabs-nav-link-title">{{ tab.title }}</p>
          </a>
          <a @click.prevent="model.closeTab(tab)" class="tabs-nav-item-close text-transparent text-sm   mr-1 hidden ">
            <i class="la la-close text-sm rounded "></i>
          </a>
        </li>
      </ul>

      <div class="tabs-nav-toolbar inline-flex justify-between   mr-12">
        <a @click.prevent="model.addTab" class="text-gray-500">
          <sv-icon class="hover:text-gray-300 hover:bg-green-500 p-2 rounded  text-sm block" name="plus"></sv-icon>
        </a>
        <a @click.prevent="closeAll" class="text-gray-500">
          <sv-icon class="hover:text-gray-300 hover:bg-red-500 p-2 rounded  text-sm block" name="close"></sv-icon>
        </a>
        <a @click.prevent="togglePreview()" class="text-gray-500">
          <sv-icon class="hover:text-gray-300 hover:bg-indigo-500 p-2 rounded  text-sm block" name="eye"></sv-icon>
        </a>
      </div>
    </div>
    <div :class="preview ? 'overflow-x-scroll' : 'flex-grow'" class="flex " v-if="tabsActive">
      <div :class="preview ? 'sv-preview ': 'flex-grow  w-full'" :key="tab.id"
           :style="containerCss(index)"
           @click="disablePreview(tab)"
           class="flex  h-full router relative"
           v-for="(tab,index) in model.tabs"
           v-show="tab.active || preview"
      >
        <a @click.prevent="splitTab(tab)" class="absolute" style="z-index: 12; top:20px ;right:20px;"
           v-if="!tab.split.enabled && tab.ready">
          <i class="zmdi zmdi-format-valign-center text-gray-500 text-2xl rotate" style="transform: rotate(90deg)"></i>
        </a>

        <div class="flex items-stretch justify-between h-full w-full ">
          <div :id="'tab-'+ tab.id + '-left'" class="flex p-0 relative max-h-screen overflow-y-scroll"
               v-if="tab.split.enabled">
            <a @click.prevent="unsplitTab(tab)" class="absolute" style="z-index: 12; top:10px ;right:10px;">
              <i class="zmdi zmdi-close text-gray-400 text-3xl "></i>
            </a>
            <component
                :is="tab.split.component"
                v-bind="tab.split.props"
                v-if="tab.split"
            ></component>
          </div>
          <div :id="'tab-'+ tab.id + '-right'"
               :style="{'width': !tab.split.enabled ? '100% !important': 'max-h-screen overflow-y-scroll'}"
               class="flex p-0 w-full">
            <component
                :is="tab.component"
                @page-title="title => model.updateTitle(tab, title)"
                v-bind="tab.props"
                v-if="tab.ready"
            ></component>
          </div>
        </div>


      </div>
    </div>
    <sv-router v-if="!tabsActive"></sv-router>
  </div>
</template>
<script>
import SvButton from '../SvButton'
import TabbedRouter from './TabbedRouter'
import SvIcon from '../SvIcon'
import SvRouter from './SvRouter'
import SvBooleanField from '../form/fields/SvBooleanField'
import Split from 'split.js'

export default {
  name: 'SvRouterContainer',
  components: { SvBooleanField, SvRouter, SvIcon, SvButton },
  data() {
    return {
      model: null,
      ready: false,
      preview: false,
    }
  },

  mounted() {
    this.ready = true
    this.$nextTick(() => {
      this.model.tabs.map(tab => {
        if (tab.split.enabled) {
          this.$nextTick(() => {
            this.split(tab)
          })
        }
      })
    })
  },

  computed: {
    tabsActive() {
      return this.$sv.$layout.tabsActive
    },

    sidebar() {
      return this.$sv.$layout.sidebar
    },

    sidebarCollapsed() {
      return this.$sv.$layout.sidebarCollapsed
    },
  },

  created() {
    this.model = new TabbedRouter(this.$superv).init(this.$route)
  },

  methods: {
    split(tab) {
      const model = this.model
      tab.split.instance = Split(
        ['#tab-' + tab.id + '-left', '#tab-' + tab.id + '-right'],
        {
          minSize: [400, 400],
          sizes: tab.split.sizes || [50, 50],
          onDragEnd: function (sizes) {
            tab.split.sizes = sizes
            model.save()
            // this.$set(tab.split, 'sizes', sizes)
          },
        },
      )
    },
    unsplitTab(tab) {
      tab.split.instance.destroy()

      this.$nextTick(() => {
        this.$set(tab, 'split', {
          component: null,
          props: null,
          enabled: false,
        })
      })
    },
    splitTab(tab) {
      this.$set(tab, 'split', {
        component: tab.component,
        props: tab.props,
        enabled: true,
      })

      tab.component = null
      this.$nextTick(() => {
        this.split(tab)
      })
    },
    containerCss(index) {
      if (this.preview) {
        return {
          'margin-left': index ? '-100px' : '',
          'z-index': 200 + index,
        }
      }
    },

    activateTab(tab) {
      if (this.preview) {
        this.preview = false
      }
      this.model.activateTab(tab)
    },

    disablePreview(tab) {
      if (!this.preview === true) {
        return
      }

      if (tab) {
        this.model.activateTab(tab)
      }

      this.preview = false
    },

    closeAll() {
      this.model.closeAll()
      this.disablePreview()
    },

    togglePreview() {
      this.preview = !this.preview
    },
  },
}
</script>

