<template>
  <div class="flex items-center justify-between rounded-t text-white   p-4"
       style="background-color: #1e5c8b; transition: all 0.5s ease-in-out">
    <div>
      {{ table.selected.length }} items selected
    </div>
    <div class="flex items-center justify-end">
      <!--      <template-->
      <!--         -->
      <!--      >-->
        <component
            v-for="action in actions"
            :is="action.component"
            :key="action.id"
            v-bind="makeProps(action)"
        ></component>
      <!--      </template>-->
      <slot name="multi-actions" :selected="table.selected"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SvTableActions',
  inject: ['table'],

  computed: {
    actions() {
      return this.table.selectionActions
    }
  },

  methods: {
    makeProps(action) {
      const props = { ...action.props } || {}

      if (props['on-complete'] === 'reload') {
        props['on-complete'] = () => { this.table.reload() }
      }

      return props
    },

    completed() {
      this.table.selected = []
    },
  },
}
</script>