
export default {
  props: {
    form: null,
    uuid: null,
    name: '',
    type: '',
    value: { default: null },
    label: '',
    sortable: { Boolean, default: false },
    placeholder: null,
    config: {
      type: Object,
      default() {
        return {
          default_value: null,
          disabled: false,
          visible: true,
        }
      },
    },
    meta: {
      type: Object,
      default() {
        return {}
      },
    },
    presenting: false,
  },
  data() {
    return {
      input: this.value,
    }
  },
  watch: {
    input(value) {
      if (this.readonly) {
        return
      }
      this.setValue(value)
    },
  },
  computed: {
    classList() {
      return this.meta.classList || []
    },
    $meta() {
      return this.meta || {}
    },
    readonly() {
      return this.$meta.disabled === true
    },
    multiple() {
      return this.$meta.multiple === true
    },
  },
  methods: {
    getPreference(key, defaultValue) {
      return localStorage.getItem(this.uuid + '.' + key) || defaultValue
    },
    setPreference(key, value) {
      localStorage.setItem(this.uuid + '.' + key, value)
      return value
    },
    setValue(value) {
      this.$emit('input', value)
    },
  },
}
