<script>
export default {
  name: 'SvBlock',
  props: ['component', 'props', 'classes', 'reload'],

  data() {
    return {
      ready: true
    }
  },

  methods: {

    debug() {
      if (!this.url) {
        this.$notifyError('no url')
        return
      }
      window.open(this.$sv.config.apiUrl + '/' + this.url + '?user=root')
    },
  },

  render(c) {
    if (!this.ready) {
      return null
    }

    return c(this.component, {
      tag: 'component',
      props: { ...this.props, reload: this.reload },
      staticClass: this.classes,
      on: {
        'title': (title) => {
          this.$emit('title', title)
        }
      }
    })
  }
}
</script>
<style>
  .sv-block {
    /*width: 100%;*/
  }
