<script>
export default {
  props: ['do'],
  name: 'OnClickOutside',
  mounted() {
    const listener = e => {
      if (e.target === this.$el || this.$el.contains(e.target)) {
        return
      }
      this.do()
    }
    document.addEventListener('mousedown', listener)
    this.$once('hook:beforeDestroy', () => {
      document.removeEventListener('mousedown', listener)
    })
  },
  render() {
    return this.$slots.default[0]
  },
}
</script>
