<template>
  <button @click.prevent="click" class="sv-btn success sm">
    Attach Selected
  </button>
</template>

<script>
export default {
  name: 'SvAttachSelectedAction',
  props: ['url', 'table', 'on-complete', 'form-data'],
  methods: {
    async click() {
      const resp = await this.$api.post(this.url, {
        items: this.selected.map(item => item.id) ,
        form_data: this.formData
      })
      if (resp.fail) {
        return this.$notifyError(resp)
      }

      this.table.reload()
      this.onComplete()
    }
  },

  computed: {
    selected() {
      return this.table.selected || []
    }
  },
}
</script>