<template>
  <div v-if="visible" class="sv-overlay">
    <on-click-outside :do="clickedOutside">
      <div :class="size"
           class="sv-modal bg-white text-white relative"
      >
        <a @click.prevent="close" class="absolute top-0 right-0 -mt-1 mr-1 text-gray-500 text-2xl" href="">&times;</a>
        <div class="sv-modal-header" v-if="$slots.header">
          <slot name="header"></slot>
        </div>
        <div class="sv-modal-body">
          <div class="flex flex-col flex-grow overflow-y-auto">
            <slot :close="close" name="body" v-if="$scopedSlots.body"></slot>
            <slot name="body" v-else-if="$slots.body"></slot>
            <slot v-else></slot>
          </div>
        </div>
        <div class="sv-modal-footer" v-if="$slots.footer || $slots.actions">
          <slot name="footer">
            <div class="flex w-full justify-end" v-if="$slots.actions">
              <slot name="actions"></slot>
            </div>
          </slot>
        </div>
      </div>
    </on-click-outside>
  </div>
</template>
<script>
import OnClickOutside from './OnClickOutside'

export default {
  name: 'SvModal',
  components: { OnClickOutside },
  props: {
    size: { type: String, default: 'w-1/2' },
    visible: { type: Boolean, default: false },
    closeOnClickOutside: { type: Boolean, default: true },
    preventBackgroundScrolling: { default: true },
  },

  data() {
    return {
      handling: false,
    }
  },

  created() {
    const escapeHandler = e => {
      if (e.key === 'Escape' && this.visible) {
        this.clickedOutside()
      }
    }
    document.addEventListener('keydown', escapeHandler)
    this.$once('hook:destroyed', () => {
      document.removeEventListener('keydown', escapeHandler)
    })
  },

  watch: {
    visible: {
      immediate: true,
      handler: function (visible) {
        if (visible) {
          this.show()
        } else {
          this.close()
        }
      },
    },
  },

  methods: {
    clickedOutside() {
      if (this.closeOnClickOutside) {
        this.close()
      }
    },
    show() {
      this.preventBackgroundScrolling &&
      document.body.style.setProperty('overflow', 'hidden')
    },
    close() {
      this.preventBackgroundScrolling &&
      document.body.style.removeProperty('overflow')

      this.$emit('update:visible', false)
      this.$emit('close')
    },
  },
}
</script>

<style scoped>

  .sv-overlay {
    position: fixed;
    overflow: auto !important;
  }

  .sv-modal {
    display: flex;
    flex-direction: column;
    position: relative;
    min-height: 240px;
    max-height: calc(100vh - 80px);
    border-radius: 5px;
    color: #333;
    min-width: 480px;
  }

  .sv-modal-body {
    display: flex;
    flex-direction: column;
    height: 100%;
    flex-grow: 1;
    /*text-align: left;*/
    /*overflow-y: auto !important;*/
  }

  .sv-modal-header {
    font-size: 1.25rem;
    color: #516371;
    padding: 1rem;
