export default {
  data() {
    return {
      currentModule: null,
      nav: '',
      ready: false,
    }
  },
  mounted() {
    this.fetch()
  },
  computed: {
    fullscreen() {
      return this.$store.$state.fullscreen
    },
  },
  methods: {
    async fetch() {
      this.ready = false
      const resp = await this.$api.get('sv/data/nav')
      if (resp.fail) {
        return this.$notifyError(resp)
      }

      this.nav = resp.data.nav
      if (resp.data.home) {
        this.$router.push(resp.data.home)
      }
      // this.nav.sections = [...this.nav.sections, ...this.nav.sections]
      this.ready = true
    },
    reset() {
      this.currentModule = null
      setTimeout(() => {}, 300)
    },
    icon(section) {
      return section.icon ? 'fa-' + section.icon : 'fa-circle'
    },
    url(url) {
      return '/' + url
    },
  },
}
