<template>
  <thead>
    <tr>
      <th class="w-8" v-if="table.selectable"></th>
      <th class="w-8" v-if="table.showIdColumn">ID</th>
      <th @dblclick="table.reload" v-for="field in table.config.fields" :class="field.classes">
        <a @click.prevent="orderBy(field.name)" v-if="field.sortable">
          <span :class="{'sort-active': orderField === field.name}" class="mr-2 inline-block">{{ field.label }}</span>
          <sv-icon :name="orderDirection === 'ASC' ? 'sort-asc' : 'sort-desc'"
                   v-if="orderField === field.name"></sv-icon>
          <sv-icon class="text-gray-400 " name="sort" v-else></sv-icon>
          </a>
        <span v-else>{{ field.label }}</span>
      </th>
      <th style=" width: 120px;" v-if="table.rowActions.length"></th>
    </tr>
  </thead>

</template>

<script>
import SvIcon from '../SvIcon'

export default {
  name: 'SvTableHead',
  components: { SvIcon },
  inject: ['table'],
  data() {
    return {
      orderField: null,
      orderDirection: null,
    }
  },
  methods: {
    orderBy(fieldName) {
      this.orderField = fieldName
      this.orderDirection = this.orderDirection === 'ASC' ? 'DESC' : 'ASC'
      this.table.orderBy(this.orderField, this.orderDirection)
    },
  },
}
</script>


<style>
  span.sort-active {
    @apply text-primary;
  }
</style>
