import Notifier from './lib/services/notifier'
import Auth from './lib/services/Auth'
import Store from './stores/Store'
import Api from './lib/services/Api'
import Router from './lib/router/Router'
import { event } from './lib/services'
import Echo from 'laravel-echo'
import Storage from './lib/services/Storage'
import LayoutManager from './components/layout/LayoutManager'

window.io = require('socket.io-client')

class SuperV {
  $debug = true
  $routes = {}

  $modal = null

  /** @var Storage **/
  $storage = null

  get api() {
    return this.$api
  }

  get events() {
    return this.$events
  }

  router() {
    return this.$router
  }

  get store() {
    return this.$store
  }

  get config() {
    return this.$config
  }

  auth() {
    return this.$auth
  }

  get notify() {
    return this.$notify
  }

  get envIsLocal() {
    return process.env.NODE_ENV === 'development'
  }

  get baseUrl() {
    if (!this.$config.baseUrl) {
      return '/'
    }

    return '/' + this.$config.baseUrl + '/'
  }

  init(config) {
    svdebug('INIT')
    this.$config = { ...config, name: config.name || 'app' }

    this.$storage = new Storage(this).init()

    this.$events = event.init()
    this.$router = new Router(this).init()
    this.$api = new Api(this).init()
    this.$store = new Store(this).init()
    this.$auth = new Auth(this).init()
    this.$notify = new Notifier(this).init()

    this.$layout = new LayoutManager(this).init()

    this.listen()
  }

  initEcho(token) {
    window.Echo = this.$echo = new Echo({
      broadcaster: 'socket.io',
      host: 'http://localhost:6001',
      auth: {
        token: this.$auth.token,
        endpoint: 'http://supreme.dev.io/sv-api/broadcasting/auth',
      },
    })
    //
    window.Echo.channel('public')
      .listen('TestEvent', e => {
        this.$notifySuccess(e.message)
      })
      .listen('.test.event', e => {
        console.log(e)
        this.$notifySuccess(e.message)
      })
    const userId = this.$store.state.user.id
    this.$echo.private('private.' + userId).listen('PrivateEvent', e => {
      this.$notifyError(e.message)
    })
    console.log('Echo init')
  }

  listen() {
    this.$events.listen('root:creating', () => this.boot())
    // this.$events.listen('login:success', token => this.initEcho(token))
  }

  async boot() {
    svdebug('BOOTING')
    this.$api.boot()
    await this.$auth.boot()
    this.$router.boot()

    svdebug('BOOTED')
  }

  state(key) {
    if (key) {
      return this.$store.$state[key] || {}
    }

    return this.$store.$state
  }

  $notifyError(error) {
    return this.$notify.error(error)
  }

  $notifySuccess(message) {
    return this.$notify.success(message)
  }
}

const superv = new SuperV()
export { superv }
