export default {
  data() {
    return {
      form: {
        email: null,
        password: null,
      },
      failed: false,
      remember: false,
    }
  },
  created() {
    this.$events.listen('login:success', this.onLoginSuccess, this._uid)
    this.$events.listen('login:failed', this.onLoginFailed, this._uid)
  },

  beforeDestroy() {
    this.$events.forget(this._uid)
  },
  methods: {
    login() {
      this.$sv.auth().attempt({
        email: this.form.email,
        password: this.form.password,
      })
    },
    onLoginSuccess(token) {
      this.$notifySuccess(this.trans('Login successful'))
      if (!this.form.remember) {
        sessionStorage.setItem(this.tokenName, token)
      }
      this.$router.home()
    },
    onLoginFailed(error) {
      this.$notifyError(this.trans(error))
    },
  },
}
