<template>
  <div>
    <div class="sv-form-group-wrapper" v-for="field in fields">

      <div class="w-full" v-if="field.meta && field.meta.full">
        <sv-field
            :field="field"
            :form="form"
            v-model="field.presenting ? field.value : form.data[field.name]"
        >
        </sv-field>
        <p class="sv-field-helper">{{ hasError(field) ? form.getError(field): ''}}</p>
      </div>

      <div :class="{'error': hasError(field)}" class="sv-form-group justify-center sm:flex-row" v-else>
        <p class="field-label hidden xs:block sm:w-1/3 max-w-md text-left p-1">{{ field.label }}</p>
        <div class="field-input w-full sm:w-2/3 max-w-2xl">
          <sv-field
              :field="field"
              :form="form"
              v-model="field.presenting ? field.value : form.data[field.name]"
          >
          </sv-field>
          <p v-if="hasError(field)" class="sv-field-helper">{{ hasError(field) ? form.getError(field): ''}}</p>
          <p class="mt-4 text-sm" style="color: #979ca8;" v-if="field.hint">{{ field.hint }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvField from './fields/SvField'

export default {
  name: 'SvFormFields',
  components: { SvField },
  props: ['fields', 'form'],
  methods: {
    hasError(field) {
      return this.form && this.form.hasError(field)
    },
  },
}
</script>
