<template>
  <button class="sv-btn orange sm flex justify-between items-center" @click.prevent="exports"><i class="text-white fa fa-external-link-alt text-white mr-2"> </i> Export </button>
</template>

<script>
import { saveAs } from 'file-saver/FileSaver'

export default {
  name: 'SvExportAction',
  props: ['url', 'filename'],
  methods: {
    async exports() {
      const resp = await this.$api.post(this.url, {}, {
        responseType: 'blob',
      })

      saveAs(new Blob([resp.$data]), this.filename + '.' + new Date().toLocaleDateString().split('/').join('.') + '.xlsx')
    },
  }
}
</script>
