<template>
  <div>
    <div v-if="presenting">
      <img :src="image_url" class="h-16" v-if="image_url">
    </div>
    <div v-else class="flex flex-col">
      <div v-if="uploadedFiles.length">
        <img :src="file.content" class="max-w-xs" v-for="file in uploadedFiles">
      </div>
      <img :src="image_url" class="max-w-xs" v-else-if="image_url">


      <div class="p-2 text-right">
        <button class="sv-btn success sm relative w-auto">
          <input :disabled="isSaving"
                 :name="name"
                 @change="change"
                 accept="image/*"
                 class="sv-input-file"
                 type="file"
          >
          {{ trans('Upload') }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import BaseField from './BaseField.js'

export default {
  name: 'SvFileField',
  mixins: [BaseField],
  props: { files: [], image_url: '' },
  data() {
    return {
      isSaving: false,
      uploadedFiles: []
    }
  },
  methods: {
    change(event) {
      this.$emit('input', event.target.files)

      readFile(event.target.files).then(files => this.uploadedFiles = files)
    },
  },
}

function readFile(files) {

  const promises = Array.from(files).map(file =>
    getImage(file).then(content => ({
      filename: file.name,
      content,
    }))
  )

  return Promise.all(promises)
}

function getImage(file) {
  return new Promise((resolve, reject) => {
    const fReader = new FileReader()
    fReader.onload = () => {
      resolve(fReader.result)
    }

    fReader.readAsDataURL(file)
  })
}
</script>

<style>
  .sv-dropbox {
    display: flex;
    height: 100%;
    min-height: 120px;
    width: 100%;
    align-items: center;
    justify-content: center;
    outline: 2px dashed #b8c2cc; /* the dash box */
    color: dimgray;
    position: relative;
    cursor: pointer;
  }

  .sv-input-file {
    opacity: 0; /* invisible but it's there! */
    width: 100%;
    height: 100%;
    position: absolute;
    cursor: pointer;
  }
</style>
