<template>
  <div class="flex flex-col min-h-screen h-full">
    <slot name="site-header">
      <sv-site-header></sv-site-header>
    </slot>

    <div class="flex flex-row justify-between w-full" style="max-width: 100%;">

      <slot name="sidebar">
        <sv-sidebar></sv-sidebar>
      </slot>

      <div class="sv-content-wrapper flex flex-col relative bg-gray-100">
        <div class="sv-content flex flex-col ">
          <sv-router-container></sv-router-container>
        </div>

        <sv-site-footer></sv-site-footer>
      </div>

    </div>

    <modals-container/>
  </div>
</template>
<script>
import SvSidebar from './SvSidebar'
import SvSiteHeader from './SvSiteHeader'
import SvSiteFooter from './SvSiteFooter'
import 'perfect-scrollbar/css/perfect-scrollbar.css'
import PerfectScrollbar from 'perfect-scrollbar'

import SvRouterContainer from '../router/SvRouterContainer'

export default {
  name: 'LayoutDefault',
  components: {
    SvRouterContainer,
    SvSiteFooter,
    SvSiteHeader,
    SvSidebar,
  },
  created() {
    this.$router.addRouteListener('layout-default', () => this.updateScroll())
  },
  mounted() {
    this.$sv.$layout.bindLayout(this.$el)
    this.initScroll()
  },

  methods: {
    initScroll() {
      if (!this.ps) {
        const container = document.querySelector('.sv-content')
        this.ps = new PerfectScrollbar(container)
      }
    },

    updateScroll() {
      this.$nextTick(() => this.ps.update())
    },
  },
}
</script>

<style>
  .sv-content-wrapper {
    padding: 64px 0 0 0;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100vh;
  }

  .sv-content {
    position: relative;
    overflow: hidden;
    width: auto;
    height: calc(100vh);
  }
</style>
