<template>
  <div>
    <button :class="'disabled' ? show : ''" @click.prevent="click" class="sv-btn success sm">
      {{ title }}
    </button>
    <sv-modal :visible.sync="show"
              @close="complete"
              size="w-2/3"
              v-if="show"
    >
      <template slot="body" slot-scope="{close}">
        <sv-loader @close="close"
                   v-bind="makeProps()"
                   v-if="url"
        >
        </sv-loader>
      </template>
    </sv-modal>
  </div>
</template>

<script>


import SvModal from '../../SvModal'
import SvLoader from '../../SvLoader'

export default {
  name: 'SvSelectionAction',
  components: { SvLoader, SvModal },
  props: ['title', 'name', 'url', 'table'],

  data() {
    return {
      show: false
    }
  },

  created() {
    this.$events.listen(`${this.name}:complete`, () => this.complete())
  },

  methods: {
    makeProps() {
      return {
        url: this.url + '?s=' + JSON.stringify(this.table.selectedIds)
      }
    },
    click() {
      this.show = true
    },
    complete() {
      if (this.onCompleteEvent) {
        this.$events.fire(this.onCompleteEvent)
      }
      this.$emit('complete')

      this.show = false
    }
  },
}
</script>

<style scoped>
  .disabled {
    opacity: 0.25;
    pointer-events: none;
  }

  .sv-btn {
    margin-right: 1rem;
  }
</style>
