<template>
  <component :is="component"
             v-bind="props"
             :class="field.classes"
             :key="field.uuid"
             @input="value => $emit('input', value)"
  ></component>
</template>

<script>
export default {
  name: 'SvField',
  props: ['field', 'form'],
  data() {
    return {
      input: null
    }
  },
  computed: {
    component() {
      const type = this.field.type.split('_').join('-')
      return `sv-${type}-field`
    },
    props() {
      return { ...this.field, classes: undefined }
    }
  },
}
</script>
