import Service from './Service'

export default class Auth extends Service {
  $authenticated = false
  $token = null

  get loggedIn() {
    return this.$authenticated && this.token
  }

  get guest() {
    return !this.$authenticated
  }

  get tokenName() {
    return this.superv.config.name + '-token'
  }

  get token() {
    return localStorage.getItem(this.tokenName) || null
  }

  set token(token) {
    localStorage.setItem(this.tokenName, token)
  }

  async boot() {
    const query = this.$router.parseQuery()
    if (query && query.token) {
      this.token = query.token
    }

    if (this.token) {
      await this.initWithExistingToken()
    }
  }

  async initWithExistingToken() {
    const resp = await this.$store.initFromUrl('sv/data/init')
    if (resp.ok) {
      this.$authenticated = true
      this.$events.fire('login:success', this.$token)
    } else {
      this.clearToken()
      this.$authenticated = false
    }
  }

  clearToken() {
    localStorage.removeItem(this.tokenName)
    sessionStorage.removeItem(this.tokenName)
  }

  async attempt(credentials) {
    const resp = await this.$api.post('sv/login', credentials)
    if (resp.data.access_token) {
      this.token = resp.data.access_token
      return await this.initWithExistingToken()
    }

    this.$authenticated = false
    this.$events.fire('login:failed', resp.$data.error.description)
  }

  listen() {
    this.$events.listen({
      'auth:token-failed': () => {
        this.clearToken()
        // this.superv.$notify.error('Session expired, please login again')
        this.superv.router().push('/login')
      },
      'logout:request': () => this.logout(),
    })
  }

  logout() {
    this.clearToken()
    this.$authenticated = false
    this.$events.fire('logout:complete')
    window.onbeforeunload = function () {}
    window.location = this.superv.config.baseUrl || '/'
  }
}
