<template>
  <div>
    <button @click.prevent="click" class="sv-btn success sm">
      {{ title }}
    </button>
    <sv-modal v-if="showModal" :visible.sync="showModal" @close="complete">
      <template slot="body" slot-scope="{close}">
        <sv-form
            :title="title"
            :fields="fields"
            :url="url"
        ></sv-form>
      </template>
    </sv-modal>
  </div>
</template>

<script>

import SvModal from './SvModal'

export default {
  name: 'SvModalForm',
  components: {  SvModal },
  props: ['identifier', 'title', 'fields', 'url'],

  data() {
    return {
      showModal: false,
      form: null
    }
  },

  created() {
    this.$events.listen(`${this.identifier}:complete`, () => this.complete())
  },

  methods: {
    click() {
      this.showModal = true
    },
    complete() {
      this.showModal = false
    },
  },
}
</script>
