<template>
  <span v-if="$label" class="sv-label">{{ $label }}</span>
</template>

<script>
export default {
  name: 'SvLabel',
  props: ['label'],
  computed: {
    $label() {
      return this.label || (this.$slots.default && this.$slots.default[0].text)
    }
  },
}
</script>

<style>
  .sv-label {
    display: inline-block;
    text-transform: uppercase;
    letter-spacing: .05em;
    color: #8795a1;
    font-size: .75rem;
    font-weight: 700;
    margin-bottom: .5rem;
    margin-left: .375rem;
  }

</style>
