<template>
  <div>
    <sv-button @click="click" :button="button">{{ title }}</sv-button>
    <sv-modal v-if="showModal" :visible.sync="showModal" @close="complete">
      <template slot="header">
        {{ title }}
      </template>
      <template slot="body" slot-scope="{close}">
        <div class="overlow-y-auto">
          <sv-loader @close="close"
                     v-bind="{url}"
                     v-if="url"
          >
          </sv-loader>
        </div>
      </template>
    </sv-modal>
  </div>
</template>

<script>

import SvModal from './SvModal'
import SvLoader from './SvLoader'
import SvButton from './SvButton'
import Button from '../lib/models/Button'

export default {
  name: 'SvModalAction',
  components: { SvButton, SvLoader, SvModal },
  props: ['identifier', 'title', 'url', 'button'],

  data() {
    return {
      showModal: false,
    }
  },

  computed: {
    button$() {
      return this.button && Button.make(this.button)
    },
    },
  created() {
    this.$events.listen(`${this.identifier}:complete`, () => this.complete())
  },

  methods: {
    click() {
      this.showModal = true
    },
    complete() {
      this.showModal = false
    }
  },
}
</script>

<style scoped>
  .disabled {
    opacity: 0.25;
    pointer-events: none;
  }

  .sv-btn {
    margin-right: 1rem;
  }
</style>
