<template>
  <div>
    <button @click.prevent="click" class="sv-btn success sm" :class="'disabled' ? show : ''">
      {{ title }}
    </button>
    <sv-modal :visible.sync="show"
              @close="complete"
              v-if="show">
      <template slot="header">
        {{ title }}
      </template>
      <template slot="body" slot-scope="{close}">
        <sv-block
            @close="close"
            v-bind="{url}"
        >
        </sv-block>
      </template>
    </sv-modal>
  </div>
</template>

<script>

import SvModal from './SvModal'

export default {
  name: 'SvRemoteAction',
  components: { SvModal },
  props: ['title', 'id', 'url', 'onCompleteEvent'],

  data() {
    return {
      show: false
    }
  },

  created() {
    this.$events.listen(`${this.id}:complete`, () => this.complete())
  },

  methods: {
    click() {
      this.show = true
    },
    complete() {
      if (this.onCompleteEvent) {
        this.$events.fire(this.onCompleteEvent)
      }
      this.$emit('complete')
    }
  },
}
</script>

<style scoped>
  .disabled {
    opacity: 0.25;
    pointer-events: none;
  }

  .sv-btn {
    margin-right: 1rem;
  }
</style>
