window.svdebug = require('./lib/utils').debug

import './css'
import { superv } from './superv'
import components from './components/index'
import defaultLayouts from './components/layout/layouts'
import LayoutDefault from './components/layout/LayoutDefault'
import LayoutBlank from './components/layout/LayoutBlank'
import SvSiteFooter from './components/layout/SvSiteFooter'
import SvSiteHeader from './components/layout/SvSiteHeader'
import BaseField from './components/form/fields/BaseField'
import BaseLoginForm from './components/layout/BaseLoginForm'
import BaseNavigation from './components/layout/BaseNavigation'
import LayoutMixin from './components/layout/LayoutMixin'
import Modalable from './components/Modalable'
import SvLoginForm from './components/layout/SvLoginForm'
import SvCard from './components/SvCard'
import SvLoader from './components/SvLoader'
import SvTable from './components/table/SvTable'
import SvModal from './components/SvModal'
import SvModalLink from './components/SvModalLink'
import SvSidebar from './components/layout/SvSidebar'
import SvSlideable from './components/SvSlideable'
import SvLoadingBlock from './components/SvLoadingBlock'
import SvPageHeader from './pages/SvPageHeader'
import SvRouterContainer from './components/router/SvRouterContainer'
import SvRouter from './components/router/SvRouter'
import SvButton from './components/SvButton'
import swal from 'sweetalert2'
import { trans } from './lib/utils'
import VModal from 'vue-js-modal'

export default {
  ready: false,

  get superv() {
    return superv
  },

  install(Vue, { config, routes, layouts, modules }) {
    superv.init(config)

    this.setupRouter(layouts, routes)

    if (modules) {
      modules.map(module => {
        Vue.use(module, { superv })
      })
    }


    Vue.prototype.$notifyError = e => superv.$notifyError(e)
    Vue.prototype.$notifySuccess = e => superv.$notifySuccess(e)
    Vue.prototype.$sv = superv

    Vue.prototype.$storage = superv.$storage

    this.registerGlobalMixin(Vue)
    this.registerComponents(Vue)

    Vue.use(VModal, { dynamic: true })

    superv.$modal = Vue.prototype.$modal
  },

  setupRouter: function(layouts, routes) {
    superv.router().setLayouts(layouts || defaultLayouts)
    superv.router().addRoutes([...(routes || []), ...require('./routes').default])
  },

  registerComponents(Vue) {
    components.map(comp => Vue.component(comp.default.name, comp.default))
  },

  registerGlobalMixin(Vue) {
    Vue.mixin({
      beforeCreate: function() {
        let self = this
        if (this.$superv) {
          return
        }

        Object.defineProperty(self, '$superv', {
          get: () => superv,
        })

        Object.defineProperty(self, '$debug', {
          get: () => msg => svdebug(msg, self.$options.name),
        })

        Object.defineProperty(self, '$events', {
          get: () => superv.events,
        })

        Object.defineProperty(self, '$api', {
          get: () => superv.api,
        })

        Object.defineProperty(self, '$store', {
          get: () => superv.store,
        })

        Object.defineProperty(self, '$router', {
          get: () => superv.router(),
        })

        Object.defineProperty(self, '$route', {
          get: () => superv.router().route,
        })
      },
      methods: {
        trans,
      },
    })
  },
}

export {
  superv,
  swal,
  BaseField,
  BaseLoginForm,
  BaseNavigation,
  LayoutMixin,
  LayoutBlank,
  LayoutDefault,
  SvCard,
  SvSiteFooter,
  SvSiteHeader,
  Modalable,
  SvButton,
  SvLoader,
  SvLoginForm,
  SvModal,
  SvSidebar,
  SvSlideable,
  SvLoadingBlock,
  SvModalLink,
  SvPageHeader,
  SvRouter,
  SvTable,
  SvRouterContainer,
}
