<template>
  <div>
    <div v-if="presenting">
      presenting...
    </div>
    <div v-else>
      <sv-button @click="showModalForm">Config</sv-button>
    </div>

    <sv-modal :visible.sync="showModal" size="w-1/2" v-if="showModal">
      <template slot="body" slot-scope="{close}">
        <div class="card slimer">
          <sv-form-fields
              :fields="config.form.props.fields"
              :form="modalForm"
          ></sv-form-fields>

        </div>
      </template>
      <template slot="footer">
        <div class="card-footer w-full">
          <sv-button @click="save">Save Config</sv-button>
        </div>
      </template>
    </sv-modal>
  </div>
</template>

<script>
import BaseField from './BaseField.js'
import SvButton from '../../SvButton'
import SvModal from '../../SvModal'
import SvFormFields from '../SvFormFields'
import FormModel from '../FormModel'

export default {
  name: 'SvModalFormField',
  components: { SvFormFields, SvModal, SvButton },
  mixins: [BaseField],
  data() {
    return {
      showModal: false,
      modalForm: {},
      savedData: {}
    }
  },

  created() {
    this.modalForm = new FormModel({ fields: this.formFields }, this.$superv, this.$set)
    this.modalForm.init()
  },

  computed: {
    formFields() {
      return this.config.form.props.fields || []
    }
  },

  methods: {
    showModalForm() {
      if (this.savedData) {
        this.modalForm.fill(this.savedData)
      }

      this.showModal = true
    },
    save() {
      this.savedData = { ...this.modalForm.data }
      this.$emit('input', JSON.stringify(this.savedData))
      this.showModal = false
    }
  },
}
</script>
