<template>
  <sv-button
      :button="button$"
      @click="click">{{ title }}
  </sv-button>
</template>
<script>
import SvButton from './SvButton'
import Button from '../lib/models/Button'

export default {
  name: 'SvAction',
  components: { SvButton },
  props: [
    'type',
    'name',
    'title',
    'target',
    'method',
    'url',
    'request',
    'on-complete',
    'button',
  ],

  computed: {
    button$() {
      return (
        this.button &&
        Button.make(this.button)
        .onClick(this.click)
        .setConfirmer(this.$superv.$notify.confirmer)
      )
    },
    method$() {
      return this.method || 'post'
    },
  },
  methods: {
    async click(event) {
      if (!this.type || this.type === 'redirect') {
        if (!this.target) {
          return this.$router.push(this.url, null, event)
        } else {
          window.history.pushState(
            null,
            null,
            this.$route.resolvedPath + '/' + this.name,
          )
          this.$events.fire('portal:' + this.target, { url: this.url })
        }
      }

      if (this.type === 'post-request') {
        const resp = await this.$api.request(
          this.method$,
          this.url,
          this.request || {},
        )
        if (resp.fail) {
          return this.$notifyError(resp)
        }

        if (this.onComplete) {
          this.onComplete()
        }

        const redirect = resp.data.redirect
        if (redirect) {
          if (redirect.target === 'blank') {
            window.open(redirect.url)
          } else {
            window.location.replace(redirect.url)
          }
        }

        if (resp.data.message) {
          this.$notifySuccess(resp.data.message)
        }
      }
    },
  },
}
</script>
