import SvLogin from './pages/SvLogin'
import SvDashboard from './pages/SvDashboard'

export default [
  {
    path: '/sv/res/*path',
    component: 'SvLoader',
    props: ({ path }) => {
      return {
        url: `sv/res/${path}` + window.location.search,
      }
    },
  },
  {
    path: '/sv/forms/*path',
    component: 'SvLoader',
    props: ({ path }) => {
      return {
        url: `sv/forms/${path}` + window.location.search,
      }
    },
  },
  {
    path: '/sv/pag/*path',
    component: 'SvLoader',
    props: ({ path }) => {
      return {
        url: `sv/pag/${path}` + window.location.search,
      }
    },
  },
  {
    path: '/login',
    meta: {
      layout: 'blank',
    },
    before: superv => {
      if (!superv.auth.loggedIn) {
        return true
      }
      superv.router().push('/')
    },
    component: SvLogin,
  },
  {
    path: '/',
    component: SvDashboard,
  },
]
