<template>
  <li :class="{
  'active': section.active,
  'has-sub': items
  }" class="section">
    <template v-if="items">
      <a :class="section.open && !minimized ? 'toggled': ''" @click="toggleSection(section)" class="flex has-sub">
        <sv-icon class="" v-if="section.icon" :name="section.icon"></sv-icon>
        <i class="zmdi zmdi-circle-o text-primary-alt" v-else></i>
        <span class="section-title">{{ section.title }}</span>
      </a>

      <sv-slideable :active="section.open" class="sv-list-sub-wrapper">
        <ul class="sv-list-sub">
          <li v-for="subpage in section.sections" :class="{'active': isCurrentUrl(subpage.url)}">
            <a :href="url(subpage.url)" @click.prevent="goTo(section, subpage, $event)" class="external-link">
              <span>{{ subpage.title }}</span>
            </a>
          </li>
        </ul>
      </sv-slideable>
    </template>
    <template v-else>
      <a :class="{'active': isCurrentUrl(section.url)}" :href="url(section.url)"
         @click.prevent="goTo(section, null, $event)"
         class="external-link">
        <sv-icon :name="section.icon" class="" v-if="section.icon"></sv-icon>
        <i class="zmdi zmdi-copy" v-else></i>
        <span class="section-title">{{ section.title }}</span>
      </a>
    </template>
  </li>
</template>

<script>
import SvSlideable from '../SvSlideable'
import SvIcon from '../SvIcon'
import { removeTrailingSlash } from '../../lib/utils/string'

export default {
  name: 'SvSidebarSection',
  components: { SvIcon, SvSlideable },
  props: ['section', 'toggleSection', 'goTo', 'minimized'],
  methods: {
    isCurrentUrl(url) {
      const isCurrent = this.url(url) === this.$route.resolvedPath

      if (isCurrent) {
        this.$set(this.section, 'active', true)
        this.$set(this.section, 'open', true)
      }

      return isCurrent
    },
    url(url) {
      return this.baseUrl + url
    },
  },
  computed: {
    items() {
      return this.section.sections
    },
    baseUrl() {
      if (!this.$superv.config.baseUrl) {
        return '/'
      }

      return '/' + removeTrailingSlash(this.$superv.config.baseUrl) + '/'
    },
  },
}
</script>
