<template>
  <div class="card">
    <div class="card-header">
      <h2 v-if="config.title">{{config.title}}</h2>
    </div>
    <div class="h-full">

      <template v-if="!table.ready">
        <sv-loading-block></sv-loading-block>
      </template>
      <template v-else>
        <div class="overflow-hidden overflow-x-auto relative">
          <table border="0"
                 cellpadding="0"
                 cellspacing="0"
                 class="table form"
          >
            <thead>
              <tr>
                <th class="w-4" v-if="table.selectable"></th>
                <th @dblclick="table.reload" v-for="field in table.config.fields">
                  <div :class="field.classes">{{ field.label }}</div>
                </th>
                <th v-if="table.rowActions.length"></th>
              </tr>
            </thead>

            <sv-table-body
                :form-data.sync="rowForms"
                :table="table"
                v-if="ready"
            ></sv-table-body>
          </table>
        </div>
        <div class="card-footer">
          <div v-if="!parent">
            <button @click="submit" class="sv-btn primary">SUBMIT</button>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import SvTableBody from './SvTableBody'

import SvLoadingBlock from '../SvLoadingBlock'
import TableModel from './TableModel'
import FormModel from '../form/FormModel'

export default {
  name: 'SvFormTable',
  components: { SvLoadingBlock, SvTableBody },
  props: {
    identifier: {},
    config: { type: Object },
    seedData: {
      type: Object, default() {return {}}
    },
  },
  provide() {
    return { table: this.table }
  },
  data() {
    return {
      ready: false,
      table: null,
      form: {},
      rowForms: {},
    }
  },
  created() {
    this.table = new TableModel(this.config, this.$superv)
    this.table.init()
    this.table.fetch().then(() => { this.makeFormData() })
  },

  computed: {
    parent() {
      return this.$parent.$parent.form
    }
  },

  methods: {
    async submit() {
      const resp = await this.$api.post(this.config.post_url, { form: this.formData, seed: this.seedData })
      if (resp.fail) {
        return this.$notifyError(resp)
      }

      this.$notifySuccess()
    },

    makeFormData() {
      this.table.rows.map(row => {
        const rowForm = new FormModel({
          identifier: this.identifier + '[' + row.id + ']',
          fields: row.fields
        }, this.$superv, this.$set)
        rowForm.initParent(this.parent, this.$set).init(this.$set)
        this.$set(this.rowForms, row.id, rowForm)
      })

      this.ready = true
    }
  },
}
</script>
