export default class Service {

  $debug = false

  constructor(superv) {
    this.superv = superv
    this.$store = superv.$store
    this.$events = superv.$events
    this.$api = superv.$api
    this.$router = superv.$router
  }

  init() {
    this.listen()

    return this
  }

  listen() {

  }

  debug(msg, data) {
    if (!this.$debug) {
      return
    }
    if (data) {
      msg += JSON.stringify(data)
    }
    svdebug(msg, this.constructor.name)
  }

}