import axios from 'axios'
import Service from './Service'
import SvDebug from '../../components/SvDebug'

export default class Api extends Service {
  init() {
    this.config = this.superv.config
    this.baseURL = this.superv.config.apiUrl

    return this
  }

  boot() {
    this.debug('BOOTING')
    const superv = this.superv // needed for axios interceptors to work

    this.$axios = axios.create({
      baseURL: this.baseURL,
    })

    this.$axios.interceptors.request.use(config => {
      config.headers.Authorization = `Bearer ${superv.auth().token}`
      return config
    })

    this.$axios.interceptors.response.use(
      response => response,
      error => {

        const response = error.response
        if (response && response.status === 401) {
          // make sure we are not trying to login
          if (
            !(
              error.config.method === 'post' &&
              /\/api\/login\/?$/.test(error.config.url)
            )
          ) {
            // token expired
            superv.events.fire('auth:token-failed')
          }
        } else if (response && response.status === 500) {

          this.superv.$modal.show(
            SvDebug,
            { response: response },
            { width: '90%', height: '80%' },
          )
        }

        return Promise.reject(error)
      },
    )

    return this
  }

  request(method, url, data, options) {
    if (this.superv.envIsLocal && url.startsWith('http')) {
      url = url.replace('http://supreme.dev.io/sv-api/', '')
    }
    return new Promise(resolve => {
      this.$axios
        .request({ url, data, method: method.toLowerCase(), ...options })
        .then(response => resolve(ApiResponse.success(response)))
        .catch(error => resolve(ApiResponse.fail(error)))
    })
  }

  get(url, options = {}) {
    return this.request('get', url, {}, options)
  }

  post(url, data, options = {}) {
    return this.request('post', url, data, options)
  }

  put(url, data) {
    return this.request('put', url, data)
  }

  patch(url, data) {
    return this.request('patch', url, data)
  }

  delete(url, data = {}) {
    return this.request('delete', url, data)
  }
}

class ApiResponse {
  constructor(response, error = null) {
    this.$response = response
    this.$error = error
  }

  get ok() {
    return !this.$error && !!this.$response
  }

  get fail() {
    return !this.ok
  }

  get response() {
    return this.$response || {}
  }

  get error() {
    return this.$error || undefined
  }

  get $data() {
    return this.response.data || {}
  }

  /**
   * Api Data
   * @returns {*|{}}
   */
  get data() {
    return this.$data.data || {}
  }

  get request() {
    return this.response.request || {}
  }

  get status() {
    return this.response.status || undefined
  }

  static fail(error) {
    return new ApiResponse(error.response, error)
  }

  static success(response) {
    return new ApiResponse(response)
  }
}
