<template>
  <div class="content">
    <header class="flex-center ">{{ title }}</header>
    <!--    <pre>{{content}}</pre>-->

    <div class="body">
      <json-viewer :expand-depth=6 :value="content"
      ></json-viewer>
    </div>
  </div>
</template>

<script>
import JsonViewer from 'vue-json-viewer'

export default {
  name: 'SvDebug',
  props: ['response'],
  components: { JsonViewer },
  computed: {
    title() {
      if (this.response.data) {
        return this.response.data.message
      }

      return 'wHHHOSPS'
    },

    content() {
      if (typeof this.response === 'object') {
        return this.response.data
      }

      return this.response
    },
  },
}
</script>

<style scoped>
  .content .body {
    padding: 0;
    overflow-y: scroll;
    max-height: 90vh;
    font-size: .75rem;
  }

  .content header {
    @apply bg-red-700 text-white text-xl font-bold py-2 px-6;
    line-height: 2.5rem;
  }

  .jv-container.jv-light {
    font-size: 12px !important;
  }
</style>
