<template>
  <div>
    <template v-if="presenting">
      <div class="sv-boolean value false text-danger"
           v-if="!value">
        <span>{{ trans('No') }}</span></div>
      <div class="sv-boolean value true text-success"
           v-else>
        <span>{{ trans('Yes') }}</span></div>
    </template>
    <div class="  bg-transparent border-transparent" v-else>
      <label class="switch">
        <input type="checkbox" v-model="input">
        <span class="switch-slider round"></span>
      </label>
    </div>
  </div>
</template>

<script scoped>
import BaseField from './BaseField.js'

export default {
  name: 'SvBooleanField',
  mixins: [BaseField],
  data() {
    return {
      input: !!this.value,
    }
  },
}
</script>

