import { debounce } from 'lodash'

export default class TableModel {
  data = {
    rows: [],
    pagination: {},
  }

  config = {
    selectable: true,
    showIdColumn: true,
    dataUrl: '',
  }

  state = {
    ready: false,
    loading: true,
    currentPage: 1,
    selected: [],
    filters: {},
    orderBy: null,
  }

  constructor(config, superv) {
    this.config.selectionActions = config.selection_actions || []
    this.config.contextActions = config.context_actions || []
    this.config.rowActions = config.row_actions || []

    this.config.filters = config.filters || []
    this.config.dataUrl = config.data_url
    this.config.showIdColumn = config.show_id_column || false
    this.config.selectable = config.selectable
    this.config.fields = config.fields || []

    this.state.currentPage = config.page || 1

    this.$superv = superv
  }

  routeChanged(newRoute) {

  }

  init() {
    this.resetState()

    this.config.selectionActions.map(action => this.injectSelf(action))
    this.config.contextActions.map(action => this.injectSelf(action))
    this.config.rowActions.map(action => this.injectSelf(action))

    this.config.filters.map(
      filter => (this.state.filters[filter.name] = filter.value),
    )

    this.$superv.events.listen('table.filters', filters => {
      this.state.filters = { ...this.state.filters, ...filters }
      this.fetch()
    })

    this.$superv.events.listen('route:changed', newRoute =>
      this.routeChanged(newRoute),
    )
    return this
  }

  resetState() {
    this.state.currentPage = 1
    this.state.selected = []
  }

  injectSelf(object) {
    if (!object.props) {
      object.props = {}
    }
    object.props.table = this
    return object
  }

  filter = debounce(() => this.reload(), 300)

  async reload() {
    this.resetState()
    const urlPath = window.location.pathname + this.buildUrl()
    window.history.pushState({ urlPath: urlPath }, urlPath, urlPath)
    await this.fetch()
  }

  loadPage(page) {
    this.state.currentPage = page

    // this.$superv.router().pushState(window.location.pathname + this.buildUrl())
    const urlPath = window.location.pathname + this.buildUrl()
    window.history.pushState({ urlPath: urlPath }, urlPath, urlPath)
    this.fetch()
  }

  orderBy(field, direction = 'ASC') {
    this.state.orderBy = { field, direction }
    this.reload()
  }

  getAvailableFilters() {
    this.config.filters.map(filter => {
      if (this.state.filters[filter.name]) {
        filter.value = this.state.filters[filter.name]
      }
    })

    return this.config.filters
  }


  async fetch() {
    this.state.loading = true

    const resp = await this.$superv.api.get(this.buildUrl(this.config.dataUrl))

    if (resp.ok) {
      this.data.rows = resp.data.rows
      this.data.pagination = resp.data.pagination || {}
      this.state.currentPage = this.data.pagination.current_page || 1
      this.state.ready = true
      this.state.loading = false
    }

    return resp
  }

  buildUrl(dataUrl = '') {
    // let dataUrl = this.config.dataUrl

    dataUrl += !dataUrl.includes('?') ? '?' : ''

    if (this.state.currentPage !== 1) {
      dataUrl += '&page=' + this.state.currentPage
    }

    const filters = this.getFilterQueryString()
    if (filters) {
      dataUrl += '&' + filters
    }

    const orderBy = this.state.orderBy
    if (orderBy) {
      dataUrl += '&order_by=' + orderBy.field + ':' + orderBy.direction
    }

    return dataUrl
  }

  getFilterQueryString() {
    const keys = Object.keys(this.state.filters)
    if (!keys.length) {
      return ''
    }

    return (
      'filters=' +
      btoa(unescape(encodeURIComponent(JSON.stringify(this.state.filters))))
    )
    // const filterMap = {}
    // this.config.filters
    // .filter(filter => filter.value)
    // .map(filter => (filterMap[filter.name] = filter.value))

    return this.serialize(this.state.filters)
  }

  serialize(obj) {
    const str = []
    for (let p in obj)
      if (obj.hasOwnProperty(p)) {
        let value = obj[p]
        if (!value) {
          continue
        }
        str.push(
          'filters[' + encodeURIComponent(p) + ']=' + encodeURIComponent(value),
        )
      }
    return str.join('&')
  }

  get rows() {
    return this.data.rows
  }

  get pagination() {
    return this.data.pagination
  }

  get hasPagination() {
    return this.pagination.total > 0
  }

  get ready() {
    return this.state.ready
  }

  get currentPage() {
    return this.state.currentPage
  }

  get selectedIds() {
    return this.state.selected.map(row => row.id)
  }

  get selected() {
    return this.state.selected
  }

  set selected(value) {
    this.state.selected = value
  }

  get loading() {
    return this.state.loading
  }

  get meta() {
    return this.config.meta || {}
  }

  get context() {
    return this.config.context || {}
  }

  get selectable() {
    return this.config.selectable
  }

  get showIdColumn() {
    return this.config.showIdColumn
  }

  get rowActions() {
    return this.config.rowActions || []
  }

  set rowActions(value) {
    this.config.rowActions = value
  }

  get contextActions() {
    return this.config.contextActions || []
  }

  get selectionActions() {
    return this.config.selectionActions || []
  }
}
