<template>
  <div class="flex justify-between items-center text-gray-500 p-3">
    <div class="mr-8">{{ trans('Showing :num Records', {'num': pagination.total})}}</div>

    <ul class="pagination pagination-info">
      <li :class="{'disabled': !hasPrevious }" class="page-item prev-page">
        <a @click.prevent="previousPage">
          <sv-icon :class="{'disabled':!hasPrevious }" class="text-2xl" name="chevron-left"></sv-icon>
        </a></li>
      <li class="page-item active">
        {{ table.currentPage }}
      </li>
      <li :class="{'disabled':!hasNext }" class="page-item next-page">
        <a @click.prevent="nextPage">
          <sv-icon name="chevron-right" class="text-2xl" :class="{'disabled':!hasNext }"></sv-icon>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import SvIcon from '../SvIcon'

export default {
  name: 'SvPagination',
  components: { SvIcon },
  inject: ['table'],
  methods: {
    previousPage() {
      this.table.loadPage(this.currentPage - 1)
    },
    nextPage() {
      this.table.loadPage(this.currentPage + 1)
    },
  },
  computed: {
    currentPage() {
      return this.table.currentPage
    },
    pagination() {
      return this.table.pagination
    },
    hasPrevious() {
      return this.currentPage > 1
    },
    hasNext() {
      return this.pagination.last_page > this.table.currentPage
    },
  },
}
</script>

<style>
  .pagination {
    display: flex;
    padding-left: 0;
    list-style: none;
    border-radius: 0.25rem;
  }

  .pagination .page-item {
    @apply border-2 border-transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 2rem;
    width: 2rem;
    @apply bg-gray-300;
    @apply mx-1;
  }

  .pagination .page-item.active {
    @apply bg-primary;
    @apply text-white;
  }

  .page-item > a {
    /*position: relative;*/
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .page-item:not(.disabled):hover {
    @apply border-primary ;
  }
</style>
