export default class Route {
  constructor(state) {
    this.$state = { ...state }
    this.component = state.component
  }

  get resolvedPath() {
    return this.$state.resolvedPath
  }

  set resolvedPath(path) {
    this.$state.resolvedPath = path
  }

  get clickEvent() {
    return this.$state.clickEvent
  }

  set clickEvent(path) {
    this.$state.clickEvent = path
  }

  set params(params) {
    this.$state.params = params
  }

  get isRedirect() {
    return !!this.$state.redirect
  }

  get redirect() {
    let redirect = this.$state.redirect
    if (!redirect) {
      return
    }

    if (typeof redirect === 'function') {
      redirect = redirect()
    }

    return redirect
  }

  set layout(layout) {
    this.$state.meta = { ...this.meta, layout: layout }
  }

  get meta() {
    return this.$state.meta || {}
  }

  get before() {
    return this.$state.before
  }

  get layout() {
    return this.meta.layout
  }

  get props() {
    return this.$state.props || {}
  }

  get params() {
    return this.$state.params || {}
  }

  get query() {
    return this.$state.query || {}
  }

  get path() {
    return this.$state.path || {}
  }

  build() {
    this.$state.query = this.parseQuery()

    let props = this.$state.props || {}
    if (typeof props === 'function') {
      props = props({ ...this.params, query: this.query })
    }

    this.$state.props = {
      ...props,
      ...this.$state.params,
      ...this.$state.query,
    }

    return this
  }

  parseQuery() {
    const queryStrings = new URLSearchParams(window.location.search)

    let query = {}
    for (let pair of queryStrings.entries()) {
      query[pair[0]] = pair[1]
    }
    return query
    // return this.parseParamsAsObject(window.location.search)
  }

  parseParamsAsObject(query) {
    query = query.substring(query.indexOf('?') + 1)

    var re = /([^&=]+)=?([^&]*)/g
    var decodeRE = /\+/g

    var decode = function (str) {
      return decodeURIComponent(str.replace(decodeRE, ' '))
    }

    var params = {},
      e
    while ((e = re.exec(query))) {
      var k = decode(e[1]),
        v = decode(e[2])
      if (k.substring(k.length - 2) === '[]') {
        k = k.substring(0, k.length - 2)
        ;(params[k] || (params[k] = [])).push(v)
      } else {
        params[k] = v
      }
    }

    var assign = function (obj, keyPath, value) {
      var lastKeyIndex = keyPath.length - 1
      for (var i = 0; i < lastKeyIndex; ++i) {
        var key = keyPath[i]
        if (!(key in obj)) {
          obj[key] = {}
        }
        obj = obj[key]
      }
      obj[keyPath[lastKeyIndex]] = value
    }

    for (var prop in params) {
      var structure = prop.split('[')
      if (structure.length > 1) {
        var levels = []
        structure.forEach(function (item, i) {
          var key = item.replace(/[?[\]\\ ]/g, '')
          levels.push(key)
        })
        assign(params, levels, params[prop])
        delete params[prop]
      }
    }
    return params
  }
}
