<template>
  <div class="flex flex-col">
    <div class="flex justify-between items-center border-b-4 border-gray-200 mb-2">
      <div class="flex justify-end p-2 pb-0 -mb-1">
        <a @click.prevent="show(tab)"
           :class="isActive(tab) ? 'active' : ''"
           class="sv-tab-item border-b-2 border-transparent uppercase"
           v-for="tab in tabs">{{tab.title}}</a>
      </div>
    </div>
    <div class="flex-grow">
      <sv-block
          v-show="isActive(tab)"
          v-for="tab in tabs"
          v-if="tab.block"
          :shared="shared"
          :fetch-on-created="tab.fetch"
          :key="tab.block.id"
          v-bind="tab.block">
      </sv-block>
    </div>
  </div>
</template>

<script>


export default {
  name: 'SvTabs',
  props: ['tabs', 'slug', 'shared', 'initial-selected'],
  data() {
    return {
      selected: null,
    }
  },
  computed: {
    cacheKey() {
      return this.$sv.config.name + ':tabs:' + this.slug + ':selected'
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      if (this.slug) {
        this.selected = localStorage.getItem(this.cacheKey)
      }
      this.selected = this.selected || this.initialSelected || this.tabs[0].title
    },
    isActive(tab) {
     return this.selected === tab.title
    },
    show(tab) {
      this.setSelected(tab)

      this.$events.fire('block:fetch', tab.block.id)
    },

    setSelected(tab) {
      this.selected = tab.title
      if (this.slug) {
        localStorage.setItem(this.cacheKey, this.selected)
      }
    }
  },
}
</script>

<style scoped>
  .sv-tab-item {
    padding: 0 10px;
    height: 35px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    line-height: 40px;
    display: inline-block;
    list-style: none;
    font-size: 14px;
    font-weight: 100;
    color: #91979b;
    position: relative;
    cursor: pointer;
    letter-spacing: 0.05rem;
    margin-right: 1rem;
  }

  .sv-tab-item.active {
    border-color: #767676;
    color: #767676;
  }

  .sv-tab-label {
    display: block;
    text-transform: uppercase;
    letter-spacing: .05em;
    color: #8795a1;
    font-size: 1rem;
    font-weight: 700;
    padding: 1rem 1rem 0.5rem;
    transition: all .3s ease-in-out;
  }


</style>
