<template>
  <trix-editor
      :value="value"
      @keydown.stop
      @trix-change="handleChange"
      @trix-initialize="initialize"
      class="trix-content"
      ref="trix"
  />
</template>

<script>
import 'trix'
import 'trix/dist/trix.css'

export default {
  name: 'trix',
  props: ['value'],
  methods: {
    initialize() {
      this.$refs.trix.editor.insertHTML(this.value)
    },

    handleChange() {
      this.$emit('change', this.$refs.trix.value)
    },
  },
}
</script>
