<script>
export default {
  name: 'SvLink',
  props: ['url', 'props'],
  data() {
    return {
      matchedRoute: {},
    }
  },
  methods: {
    push(event) {
      this.$router.push(this.url, { props: this.props })
      event.preventDefault()
    },
  },
  render(c) {
    return c(
      'a',
      {
        staticClass: 'block py-2 px-4 w-full h-full',
        on: {
          click: this.push,
        },
      },
      [this.$slots.default[0]],
    )
  },
}
</script>
