export function uppercaseFirst(str) {
  if (!str) {
    return ''
  }
  return str.replace(new RegExp('^' + str[0] + ''), str[0].toUpperCase())
}

/**
 * @param string
 * @returns {void|*}
 */
export function removeTrailingSlash(string) {
  if (string === '/' || string === '') {
    return string
  }
  return string.replace(new RegExp('/' + '+$'), '')
}

/**
 * @param string
 */
export function removeLeadingSlash(string) {
  string = string.replace(/^\/+/g, '')

  return string
}

/**
 * @param url
 */
export function parseUrl(url) {
  let params = {}
  url
  .replace('?', '')
  .split('&')
  .map(raw => {
    let key, value
    ;[key, value] = raw.split('=')
    params[key] = value
  })
  return params
}

/**
 * @param query
 */
export function parseHttpQuery(query) {
  query = query.substring(query.indexOf('?') + 1)

  var re = /([^&=]+)=?([^&]*)/g
  var decodeRE = /\+/g

  var decode = function (str) {
    return decodeURIComponent(str.replace(decodeRE, ' '))
  }

  var params = {},
    e
  while ((e = re.exec(query))) {
    var k = decode(e[1]),
      v = decode(e[2])
    if (k.substring(k.length - 2) === '[]') {
      k = k.substring(0, k.length - 2)
      ;(params[k] || (params[k] = [])).push(v)
    } else {
      params[k] = v
    }
  }

  var assign = function (obj, keyPath, value) {
    var lastKeyIndex = keyPath.length - 1
    for (var i = 0; i < lastKeyIndex; ++i) {
      var key = keyPath[i]
      if (!(key in obj)) {
        obj[key] = {}
      }
      obj = obj[key]
    }
    obj[keyPath[lastKeyIndex]] = value
  }

  for (var prop in params) {
    var structure = prop.split('[')
    if (structure.length > 1) {
      var levels = []
      structure.forEach(function (item, i) {
        var key = item.replace(/[?[\]\\ ]/g, '')
        levels.push(key)
      })
      assign(params, levels, params[prop])
      delete params[prop]
    }
  }
  return params
}

export function buildUrl(query) {
  return Object.keys(query)
  .map(key => {
    let value = query[key]
    return key + '=' + query[key]
  })
  .join('&')
}
