<template>
  <component :is="componentName" v-bind="propsAll" v-if="ready"></component>
</template>
<script>
export default {
  name: 'SvPortal',
  props: {
    name: { type: String },
    url: { type: String },
    subprops: {
      type: Object,
      defaut() {
        return {}
      },
    },
  },
  data() {
    return {
      ready: false,
      currentUrl: null,
      matchedRoute: {},
    }
  },

  computed: {
    componentName() {
      return this.matchedRoute && this.matchedRoute.component
    },
    propsAll() {
      const routeProps = (this.matchedRoute && this.matchedRoute.props) || {}
      return { ...this.subprobs, ...routeProps }
    },
  },

  watch: {
    url: {
      immediate: true,
      handler: function (url) {
        this.push(url)
      },
    },
  },
  methods: {
    push(url) {
      const matchedRoute = this.$router.match('/' + url)

      if (matchedRoute) {
        matchedRoute.build()
        this.matchedRoute = null
        this.$nextTick(() => {
          this.ready = true
          this.matchedRoute = matchedRoute
        })
      } else {
        this.$notifyError('Route not found for url: ' + url)
      }
    },
  },
}
</script>
