import Service from '../lib/services/Service'
import Vue from 'vue'

const stub = {
  authenticated: false,
  user: null,
}

export default class Store extends Service {
  $handlers = {}
  $state = {}

  init() {
    this.reset()

    return this
  }

  reset() {
    this.$state = Vue.observable(stub)
  }

  get authenticated() {
    return this.$state.authenticated
  }

  set authenticated(is) {
    this.$state.authenticated = is
  }

  async initFromUrl(url) {
    const resp = await this.$api.get(url)
    if (resp.ok) {
      await this.setState(resp.data)
      // boot
      Object.keys(this.$handlers).map(async key => {
        if (typeof this.$handlers[key].boot === 'function') {
          this.$handlers[key].boot()
        }
      })
    }

    return resp
  }

  setStateHandlers(handlers) {
    this.$handlers = handlers
  }

  addStateHandler(forKey, handler) {
    this.$handlers[forKey] = handler
    return this
  }

  get state() {
    return this.$state
  }

  set(key, value) {
    Object.assign(this.$state, { [key]: value })
    return this
  }

  get(key) {
    return this.$state[key]
  }

  async setState(state) {
    Object.keys(state).map(async key => {
      const stateItem = { [key]: state[key] }
      if (this.$handlers[key]) {
        const handler = this.$handlers[key]
        await handler.init(stateItem)
        Object.assign(this.$state, { [key]: handler })
      } else {
        Object.assign(this.$state, stateItem)
      }
    })
    Object.keys(this.$handlers).map(async key => {
      if (!this.$state[key]) {
        const handler = this.$handlers[key]
        await handler.init()
        Object.assign(this.$state, { [key]: handler })
      }
    })
    return this
  }
}
