<template>
  <div class="w-full flex flex-col">
    <div class="flex justify-between items-center">
      <a @click.prevent="add"
         class="tab-link  border-b-2 border-transparent"
         style="padding-bottom: 1rem !important;"
      >ADD</a>
    </div>
    <div class="w-full mb-1 flex justify-between items-center " v-for="row in rows">
      <input @change="sync" class="w-1/3 sv-dictionary key" v-model="row.key">
      <input @change="sync" class="w-2/3 sv-dictionary value" v-model="row.value">
    </div>
  </div>
</template>

<script>
import BaseField from './BaseField.js'

export default {
  name: 'SvDictionaryField',
  mixins: [BaseField],
  data() {
    return {
      selected: null,
      rows: []
    }
  },
  created() {
    Object.keys((this.value || {})).map(key => {
      this.rows.push({ key, value: this.value[key] })
    })
  },

  methods: {
    sync() {
      let input = {}
      this.rows.map(row => input[row.key] = row.value)
      this.$emit('input', JSON.stringify(input))
    },
    add() {
      this.rows.push({ key: 'key', value: 'value' })
    }
  },
}
</script>

