<template>
  <div>
    <a @click.prevent="click">
      {{ title }}
    </a>
    <sv-modal :visible.sync="show" size="w-2/3" v-if="show">
      <template slot="body" slot-scope="{close}">
        <div v-html="content" v-if="content"></div>
        <sv-block @close="close"
                  v-bind="{url}"
                  v-if="url"
        >
        </sv-block>
      </template>
    </sv-modal>
  </div>
</template>

<script>

import SvModal from './SvModal'

export default {
  name: 'SvModalButton',
  components: { SvModal },
  props: ['content', 'url'],

  data() {
    return {
      show: false
    }
  },

  computed: {
    title() {
      return (this.$slots.default && this.$slots.default[0].text) || 'Contents'
    }
  },

  methods: {
    click() {
      this.show = true
    },
  },
}
</script>
