import Service from '../../lib/services/Service'
import Vue from 'vue'

export default class TabbedRouter extends Service {
  $state = null

  get tabs() {
    return this.$state.tabs
  }

  get activeTab() {
    return this.tabs.find(tab => tab.active)
    // return this.$state.activeTab
  }

  get generateId() {
    let min = 10000
    let max = 99999
    min = Math.ceil(min)
    max = Math.floor(max)
    return Math.floor(Math.random() * (max - min)) + min
  }

  init(route = null) {
    this.$state = Vue.observable({ tabs: [], activeTab: null })

    const tabs = this.superv.$storage.get('router_tabs')
    if (tabs) {
      this.$state.tabs = tabs
    }
    this.$events.listen('route:changed', newRoute =>
      this.activateRoute(newRoute),
    )

    if (route) {
      this.activateRoute(route)
    }


    return this
  }

  activateRoute(newRoute) {
    if (newRoute.clickEvent && newRoute.clickEvent.altKey) {
      this.addTab()
    }

    if (this.tabs.length === 0) {
      this.addTab()
    }

    this.activeTab.ready = false
    Vue.nextTick(() => {
      this.activateTabComponent(newRoute)
    })
  }

  makeTab() {
    return {
      id: this.generateId,
      title: '...',
      active: false,
      ready: false,
      component: null,
      props: null,
      split: { enabled: false, component: null, props: null, sizes: [] },
    }
  }

  addTab() {
    const tab = Vue.observable(this.makeTab())

    this.tabs.map(_tab => (_tab.active = false))
    this.tabs.push(tab)

    this.activateTab(tab)

    this.save()

    return tab
  }

  closeTab(tab) {
    const index = this.tabs.indexOf(tab)
    if (index > -1) {
      this.tabs.splice(index, 1)
    }
    if (tab.active && this.tabs.length) {
      this.activateTab(this.tabs[this.tabs.length - 1])
    }

    this.save()
  }

  duplicateTab(tab) {
    const duplicate = { ...tab, id: this.generateId }
    this.tabs.push(duplicate)

    this.activateTab(duplicate)
  }

  save() {
    this.superv.$storage.set('router_tabs', this.tabs)
  }

  updateTitle(tab, title) {
    tab.title = title
    this.save()
  }

  closeAll() {
    this.$state.tabs = []

    this.save()
  }

  activateTab(tab) {
    this.tabs.map(_tab => {
      _tab.active = false
    })

    tab.active = true

    this.$state.activeTab = tab

    this.save()
  }

  activateTabComponent(route) {
    this.activeTab.component = route.component
    this.activeTab.props = { ...route.props }
    this.activeTab.ready = true
  }
}
