export default class Button {

  defaults = {
    color: 'lightest',
    size: 'md'
  }
  constructor(state) {
    this.state = state
  }

  get class() {
    return this.state.class
  }

  get title() {
    return this.state.title
  }

  get color() {
    return this.state.color || this.defaults.color
  }

  get size() {
    return this.state.size || this.defaults.size
  }

  get icon() {
    return this.state.icon || null
  }

  get confirm() {
    return this.state.confirm || null
  }

  static make(state) {
    return new Button(state)
  }

  click(event, confirmed = false) {
    if (this.onClick) {
      // handle confirmation
      if (this.confirm && !confirmed) {
        this.confirmer(
          this.confirm,
          '',
          {
            confirmButtonText: 'Confirm',
            cancelButtonText: 'Cancel',
            type: 'error',
          },
        ).then((result) => {
          if (result.value) {
            this.click(event, true)
          }
        })

        return
      }

      (this.onClick)(event)
    }
  }

  onClick(action) {
    this.onClick = action
    return this
  }

  setConfirmer(confirmer) {
    this.confirmer = confirmer
    return this
  }

  handleConfirmation() {

  }
}