<template>
  <div class="">
    <div class="flex justify-between items-center">
      <div class="flex justify-end mb-2">
        <a :class="selected === key ?'border-blue' : ''"
           @click.prevent="show(key)"
           class="sv-dictionary-key border-b-2 border-transparent"
           style="padding-bottom: 0.25rem !important;"
           v-for="key in tabs">{{key}}</a>
      </div>
    </div>
    <div
        class="mb-1 flex justify-between items-center relative"
        v-for="key in keys"
        v-show="selected === key || showAll"
    >
      <div class="sv-dictionary-key left" v-if="showAll">{{ key }}</div>
      <!--<textarea v-if="config.rows > 1" :class="showAll ? 's' : ''"-->
      <!--class="sv-dictionary-value"-->
      <!--:rows="config.rows"-->
      <!--v-model="input[key]"-->
      <!--&gt;-->
      <!--</textarea>-->
      <sv-textarea-field
          :class="showAll ? 's' : ''" class="sv-dictionary-value"
          v-if="config.rows > 1"
          v-model="input[key]"
      >

      </sv-textarea-field>
      <input :class="showAll ? 's' : ''" :placeholder="placeholder"
             class="sv-dictionary-value"
             v-else
             v-model="input[key]"
      >
    </div>
  </div>
</template>

<script>
import BaseField from './BaseField.js'

export default {
  name: 'SvArrayField',
  mixins: [BaseField],
  data() {
    return {
      selected: null,
    }
  },
  created() {
    this.input = this.config.values || this.value || {}
    this.selected = this.getPreference('selected', this.keys[0])
  },
  computed: {
    keys() {
      return Object.keys(this.input)
    },
    showAll() {
      return this.selected === 'All'
    },
    tabs() {
      return [...this.keys, 'All']
    },
  },
  methods: {
    show(key) {
      this.selected = this.setPreference('selected', key)
    }
  },
}
</script>

