<template>
    <sv-button
        :button="button$"
        @click="click">{{ title }}
    </sv-button>
</template>
<script>
import SvButton from './SvButton'
import Button from '../lib/models/Button'

export default {
  name: 'SvRequestAction',
  components: { SvButton },
  props: ['name', 'title', 'method', 'url', 'request', 'on-complete', 'button'],

  computed: {
    button$() {
      return this.button && Button.make(this.button)
      .onClick(this.click)
      .setConfirmer(this.$superv.$notify.confirmer)
    },
    method$() {
      return this.method || 'post'
    }
  },
  methods: {
    async click() {
      const resp = await this.$api.request(this.method$, this.url, this.request || {})
      if (resp.fail) {
        return this.$notifyError(resp)
      }

      if (this.onComplete) {
        this.onComplete()
      }

      const redirect = resp.data.redirect
      if (redirect) {
        if (redirect.target === 'blank') {
          window.open(redirect.url)
        } else {
          window.location.replace(redirect.url)
        }
      }

      if (resp.data.message) {
        this.$notifySuccess(resp.data.message)
      }
    }
  },
}
</script>
