<template>
  <sv-loader :subprops="subprops" :url="currentUrl" v-if="currentUrl"></sv-loader>
</template>
<script>
import SvLoader from '../SvLoader'
import SvCard from '../SvCard'

export default {
  name: 'SvRouterPortal',
  components: { SvCard, SvLoader },
  props: {
    name: { type: String },
    url: { type: String },
  },

  data() {
    return {
      currentUrl: null,
      subprops: {},
    }
  },

  created() {
    this.$events.listen('portal:' + this.name, ({ url, subprops }) =>
      this.push(url, subprops),
    )

    if (this.url) {
      this.push(this.url)
    }
  },

  methods: {
    push(url, subprops = {}) {
      this.currentUrl = null
      this.$nextTick(() => {
        this.currentUrl = url
        this.subprops = subprops
      })
    },
  },
}
</script>
