<template>
  <div v-if="showModal" class="overlay">
    <div class="modalable bg-white text-white relative"
         :class="{
           'w-1/2': size === 'normal',
           'lg:w-1/2 w-2/3': size === 'wide',
            'lg:w-1/2 w-full m-10': size === 'responsive',
           'w-full m-10': size === 'full'
           }"
    >
      <a @click.prevent="dismiss" href="" class="absolute top-0 right-0 mt-1 mr-3 text-gray-500 text-3xl">&times;</a>
      <div class="modalable-header">
        <slot name="header"></slot>
      </div>
      <div class="modalable-body">
        <slot v-if="$scopedSlots.body" name="body" :dismiss="dismiss"></slot>
        <slot v-else></slot>
      </div>
      <div class="modalable-footer">
        <slot name="footer">
          <div v-if="$slots.actions" class="flex w-full justify-end">
            <slot name="actions"></slot>
          </div>
        </slot>
      </div>
    </div>
  </div>
</template>
<script>
import OnClickOutside from './OnClickOutside'

export default {
  name: 'Modalable',
  components: { OnClickOutside },
  props: {
    size: { type: String, default: 'normal' },
    visible: { type: Boolean, default: false },
    preventBackgroundScrolling: { default: true }
  },

  data() {
    return {
      showModal: false,
    }
  },

  created() {
    // this.showModal = this.visible

    const escapeHandler = e => {
      if (e.key === 'Escape' && this.show) {
        this.dismiss()
      }
    }
    document.addEventListener('keydown', escapeHandler)
    this.$once('hook:destroyed', () => {
      document.removeEventListener('keydown', escapeHandler)
    })
  },

  watch: {
    visible: {
      immediate: true,
      handler: function (visible) {
        this.showModal = visible
      }
    },
    showModal: {
      immediate: true,
      handler: function (showModal) {
        if (showModal) {
          this.preventBackgroundScrolling &&
          document.body.style.setProperty('overflow', 'hidden')
        } else {
          this.preventBackgroundScrolling &&
          document.body.style.removeProperty('overflow')
        }
        this.$emit('update:visible', showModal )
      }
    }
  },

  methods: {
    show() {
      this.showModal = true
    },
    dismiss() {
      this.showModal = false
      this.$emit('dismiss') // deprecated
      this.$emit('close')
    },
  },

}
</script>

<style scoped>
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2000;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .modalable {
    display: flex;
    flex-direction: column;
    position: relative;
    min-height: 240px;
    border-radius: 5px;
    color: #333;
    min-width: 480px;
  }

  .modalable-body {
    display: flex;
    flex-direction: column;
    min-height: 200px;
    text-align: left;
  }

  .modalable-header {
    font-size: 1.25rem;
    color: #516371;
    padding: 1rem;
    border-bottom-width: 1px;
    border-color: #f1f5f8;
    text-transform: uppercase;
    min-height: 3rem;
  }

  .modalable-footer {
    width: 100%;
    border-top-width: 1px;
    border-color: #f1f5f8;
