export default {
  data() {
    return {
      current: null
    }
  },
  created() {
    this.$events.listen('route:matched', (matched) => this.routeMatched(matched))
    this.$events.fire('root:creating')
  },

  render(c) {
    if (!this.current) {
      return this._e()
    }

    if (this.layouts && this.layouts[this.current.name]) {
      return c(this.layouts[this.current.name], {})
    }

    const layout = c(this.current.component, {
      tag: 'component',
      props: this.current.props || {}
    })

    return layout
  },

  methods: {
    routeMatched(matched) {
      // this.$debug('route matched')
      const layout = matched.meta && matched.meta.layout
      if (layout) {
        if (this.current && layout.name !== this.current.name) {
          // this.$debug('layout changed to: ' + layout.name)
          this.current = null
        }
        // this.$debug('rendering layout: ' + layout.name)
        this.current = matched.meta.layout
      }
    },
  },
}