<template>
  <div class="sv-page-header px-8 pb-6 pt-3 flex flex-col  bg-gray-100">
    <div class="flex flex-row justify-between">
      <div class="flex items-center pt-2" style="margin-left: 0;">
        <!--        <a class="mr-3 text-gray-400 hover:text-primary-alt" @click="redirectToIndex">-->
        <!--          <sv-icon name="long-arrow-left" class="text-xl mt-1 font-bold"></sv-icon>-->
        <!--        </a>-->
        <template v-if="meta && meta.parent">
          <a :href="meta.parent.url" class="p-1 border-b-2 border-transparent hover:border-gray-300">
            <header>{{ meta.parent.title }}</header>
          </a>
          <header class="breadcrumbs-separator"> ></header>
        </template>
        <header @dblclick="reload$" class="p-1 border-b-2 border-transparent">{{ title }}</header>
      </div>
      <div class="sv-page-actions pt-2">
        <slot name="actions">
          <component
              :is="action.component"
              :key="action.uuid"
              v-bind="hydrate(action).props"
              v-for="action in actions"
          >
          </component>
        </slot>
      </div>
    </div>


  </div>
</template>

<script>
import SvIcon from '../components/SvIcon'
import SvDropdown from '../components/SvDropdown'
import SvPageNav from './SvPageNav'
import SvButton from '../components/SvButton'

export default {
  name: 'SvPageHeader',
  components: { SvButton, SvPageNav, SvDropdown, SvIcon },
  props: ['title', 'actions', 'links', 'reload', 'meta'],
  data() {
    return {}
  },
  computed: {
    page() {
      return this.$parent.$props
    },
  },
  methods: {
    reload$() {
      if (this.reload) {
        this.reload()
      }
    },
    hydrate(action) {
      if (!action.props) {
        action.props = {}
      }
      if (
        action.props['on-complete'] &&
        action.props['on-complete'] === 'reload'
      ) {
        action.props['on-complete'] = this.reload
      }

      return action
    },
    redirectToIndex() {
      if (this.links) {
        this.$router.push(this.links.index)
      } else {
        this.$router.back()
      }
    },
  },
}
</script>

