<template>
  <button :class="[color$, size$ || 'sm',  icon$ && 'icon', button && button.class]"
          @click.prevent="click"
          class="sv-btn">
    <sv-icon :class="[title$ && 'mr-2' ]" :name="icon$" @click.prevent="click" class="text-xl" v-if="icon$"></sv-icon>
    {{ title$ }}
  </button>
</template>

<script>
import Icon from './SvIcon'

export default {
  name: 'SvButton',
  components: { 'sv-icon': Icon },
  props: [
    'url',
    'action',
    'icon',
    'title',
    'target',
    'modal',
    'onCompleteEvent',
    'color',
    'size',
    'confirm',
    'button',
  ],
  computed: {
    icon$() {
      return this.button ? this.button.icon : this.icon
    },
    color$() {
      return this.button ? this.button.color : this.color
    },
    size$() {
      return this.button ? this.button.size : this.size
    },
    confirm$() {
      return this.button ? this.button.confirm : this.confirm
    },
    title$() {
      if (this.button) {
        return this.button.title
      }

      return this.title || (this.$slots.default && this.$slots.default[0].text)
    },
  },
  methods: {
    click(event) {
      if (this.button && this.button.click) {
        this.button.click(event)
        return
      }

      if (!this.url) {
        return this.$emit('click', event)
      }
      if (this.target === 'blank') {
        window.open(this.url)
      } else if (this.target === 'ajax') {
        this.ajax()
      } else {
        this.$router.push(this.url, null, event)
      }
    },
    async ajax() {
      const resp = await this.$api.get(this.url)
      if (resp.fail) {
        this.$notifyError(resp)
      } else {
        this.onComplete(resp)
      }
    },
    onComplete(resp) {
      if (this.onCompleteEvent) {
        this.$events.fire(this.onCompleteEvent, resp)
      }
      this.$emit('complete', resp)
    },
  },
}
</script>
