<template>
  <div :class="{mounted: isMounted, mounting: !isMounted}" class="sv-page flex-grow">
    <sv-page-header
        :title="meta.title"
        :meta="meta"
        :actions="actions"
        :links="links"
        :reload="reload"
    ></sv-page-header>


    <div class="sv-page-body flex-grow p-0 sm:p-2 sm:pb-4 md:p-4 md:pb-12">
      <div class="sv-page-sections flex flex-col flex-grow mt-2 bg-white">
        <ul class="flex items-center border-b-2 border-gray-200 p-5 pb-0">
          <li :class="{'font-semibold border-primary': section.active === true }"
              class="flex  justify-between items-center  -mb-2px  mx-2  border-b-2 border-transparent"
              v-for="section in sections"
          >
            <a
                @click.prevent="loadSection(section)"
                class="font-semibold block  mr-1 flex-grow pb-4 pr-0  pl-2   "
            >
              <span :class="[section.active ? 'text-primary' : 'text-gray-500']"
                    class="text-sm font-semibold uppercase tracking-widest"
              >{{ section.title }}</span>
            </a>
          </li>
        </ul>

        <div class="section-body pt-6">
          <component
              :is="block.component"
              :key="block.component"
              v-bind="block.props"
              v-for="block in blocks"
          ></component>
        </div>
      </div>

      <!--      <template v-if="url">-->
      <!--        <sv-portal v-if="portal" :url="url"></sv-portal>-->
      <!--        <div v-else class="card-slim shadow-1">-->
      <!--          <sv-loader :url="url"></sv-loader>-->
      <!--        </div>-->
      <!--      </template>-->


    </div>

  </div>
</template>

<script>
import SvIcon from '../components/SvIcon'
import SvPageNav from './SvPageNav'
import SvPortal from '../components/SvPortal'
import SvPageHeader from './SvPageHeader'

export default {
  name: 'SvPage',
  components: { SvPageHeader, SvPortal, SvPageNav, SvIcon },
  props: [
    'tab',
    'tab-id',
    'meta',
    'sections',
    'actions',
    'blocks',
    'links',
    'reload',
  ],
  data() {
    return {
      isMounted: true,
      viewing: true,
      url: null,
      portal: null,
    }
  },
  created() {
    this.$emit('page-title', this.meta.title)
  },
  mounted() {
    this.$nextTick(() => {
      this.isMounted = true
      if (this.sections) {
        this.sections.map(_section => {
          if (_section.default) {
            this.loadSection(_section, false)
          }
        })
      }
    })
  },


  methods: {
    loadSection(section, history = true) {
      // this.parent.$data.portal = !!section.portal
      //
      // this.parent.$data.url = null

      // this.$nextTick(() => (this.parent.$data.url = section.url))
      if (history) {
        window.history.pushState(
          null,
          null,
          this.$superv.baseUrl + this.meta.url + '/' + section.identifier,
        )
      }
      // this.$notifySuccess('/' + this.meta.url + '/' + section.identifier)
      this.$events.fire(section.target, { url: section.url })
      this.sections.map(_section => {
        this.$set(_section, 'active', _section.url === section.url)
      })
    },
  },
}
</script>

<style>
  .sv-page {
    display: flex;
    flex-direction: column;
    /*overflow-x: auto;*/
    width: 100%;
    background: #eef2f6;
    /*background: red;*/
    z-index: 10;
  }

  .sv-page.mounting {
    opacity: 0;
    visibility: hidden;
    -webkit-transition: opacity 900ms, visibility 900ms;
    transition: opacity 900ms, visibility 900ms;
  }

  .sv-page.mounted {
    visibility: visible;
    opacity: 1;
  }

  .sv-page-actions > button {
    padding: 0.375rem 0.75rem;
    text-transform: uppercase;
    letter-spacing: 0.1em !important;
  }

  .sv-page-body {
    /*min-height: calc(100vh/2);*/
  }
</style>
