  font-size: 0.625rem;
  text-align: center;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  width: auto;
  min-width: 100px;
  border-radius: 0.5rem;
  display: inline-block;
  letter-spacing: 0.07em;
}

