<template>
  <div class="inline-flex-center">
    <input :disabled="config.disabled"
           :id="id"
           :value="checkedValue"
           @change="changed"
           class="styled-checkbox"
           type="checkbox"
    >
    <label :for="id">{{ label }}</label>
  </div>

</template>

<script>
import BaseField from './BaseField.js'

export default {
  name: 'SvCheckboxField',
  mixins: [BaseField],
  props: ['checkedValue'],

  data() {
    return {
      id: null,
      checkedProxy: false
    }
  },

  mounted() {
    this.id = this._uid
  },
  created() {
    // set the checked proxy if we start with this value included.
    if ((this.value || []).includes(this.checkedValue)) {
      this.checkedProxy = true
    }
  },

  methods: {
    changed() {
      this.checkedProxy = !this.checkedProxy
      let value = [].concat(this.value) // copy so we dont mutate directly
      if (!this.checkedProxy && value.includes(this.checkedValue)) {
        value.splice(value.indexOf(this.checkedValue), 1)
      } else {
        value.push(this.checkedValue)
      }

      this.setValue(value)
    },
  },
}
</script>
<style scoped>
  .styled-checkbox {
    position: absolute;
    opacity: 0;

    & + label {
      position: relative;
      cursor: pointer;
      padding: 0;
    }

    & + label:before {
      @apply bg-gray-300;
      content: '';
      margin-right: 10px;
      display: inline-block;
      vertical-align: text-top;
      width: 16px;
      height: 16px;
    }

    &:hover + label:before {
      @apply bg-primary;
    }

    &:focus + label:before {
      /*box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);*/
    }

    &:checked + label:before {
      @apply bg-primary;
    }

    &:disabled + label {
      color: #b8b8b8;
      cursor: auto;
    }

    &:disabled + label:before {
      box-shadow: none;
      background: #ddd;
    }

    &:checked + label:after {
      content: '';
      position: absolute;
      left: 3px;
      top: 9px;
      background: white;
      width: 2px;
      height: 2px;
      box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white,
      4px -6px 0 white, 4px -8px 0 white;
      transform: rotate(45deg);
    }
  }
</style>
