import swal from 'sweetalert2'
import Service from './Service'
// import Toasted from 'toastedjs'
// import 'toastedjs/dist/toasted.min.css'
import { trans } from '../utils'
import izitoast from 'izitoast'
import 'izitoast/dist/css/iziToast.css'

export default class Notifier extends Service {
  get confirmer() {
    return () => {
      return swal.fire({
        title: trans('Are you sure?'),
        text: trans("You won't be able to revert this!"),
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        cancelButtonText: trans('Cancel'),
        confirmButtonText: trans('Yes, delete it!'),
      })
    }
  }

  success(message, timeout = 4000) {
    // return new Toasted({
    //   position: 'bottom-right',
    //   theme: 'bulma',
    //   timeout,
    // }).success(message)

    izitoast.success({
      message: message,
      position: 'bottomLeft',
      timeout: timeout,
    })

    // this.show(message, {
    //   position: 'top-end',
    //   toast: true,
    //   showConfirmButton: false,
    //   timer: timeout,
    // })
    // this.$notify.success({ title: 'Success', message })
  }

  show(message, params) {
    const def = {
      type: 'success',
      title: '',
      html: message,
    }
    swal({ ...def, ...params })
    // this.$notify.success({ title: 'Success', message })
  }

  warn(message) {
    swal({
      position: 'top-end',
      type: 'warning',
      toast: true,
      text: message,
      showConfirmButton: false,
      timer: 3000,
    })
    // this.$notify.warning({ title: 'Warning', message })
  }

  error(err) {
    let message
    if (typeof err === 'object') {
      if (err.constructor.name === 'ApiResponse') {
        err = err.$error
      }
    }
    if (err.message && !err.response) {
      message = err.message
    } else if (err.response) {
      if (err.response.data) {
        const data = err.response.data
        if (typeof data.errors !== 'undefined') {
          message = Object.values(data.errors).join('<br>')
        } else if (typeof data.error !== 'undefined') {
          if (data.error.description) {
            message = data.error.description
          } else if (data.error.list) {
            message = Object.values(data.error.list).join('<br>\n')
          }
        }
      }
    } else {
      message = err
    }
    // this.$notify.error({
    //   title: 'Error',
    //   message,
    //   dangerouslyUseHTMLString: true,
    // })
    if (!message) {
      return
    }
    izitoast.error({
      message: message,
      position: 'bottomLeft',
    })
    //
    // izitoast.question({
    //   timeout: 20000,
    //   close: false,
    //   overlay: true,
    //   displayMode: 'once',
    //   id: 'question',
    //   zindex: 999,
    //   title: 'Hey',
    //   message: 'Are you sure about that?',
    //   position: 'center',
    //   buttons: [
    //     ['<button><b>YES</b></button>', function (instance, toast) {
    //
    //       instance.hide({ transitionOut: 'fadeOut' }, toast, 'yesbutton');
    //
    //     }, true],
    //     ['<button>NO</button>', function (instance, toast) {
    //
    //       instance.hide({ transitionOut: 'fadeOut' }, toast, 'nobutton');
    //
    //     }],
    //   ],
    //   onClosing: function(instance, toast, closedBy){
    //     console.info('Closing | closedBy: ' + closedBy);
    //   },
    //   onClosed: function(instance, toast, closedBy){
    //     console.info('Closed | closedBy: ' + closedBy);
    //   }
    // });
    // swal({
    //   type: 'error',
    //   title: 'Error',
    //   html: message,
    //   showConfirmButton: true,
    //   timer: 5000,
    // })

    // new Toasted({
    //   position: 'bottom-right',
    //   theme: 'bulma',
    //   className: 'text-white'
    // }).error(message)

    return message
  }
}
