<script>
import { buildUrl } from '../lib/utils'

export default {
  name: 'SvLoader',
  props: ['tab', 'tab-id', 'request', 'url', 'listenEvent', 'subprops'],

  data() {
    return {
      block: {},
      ready: false,
      previous: {},
      filters: {},
    }
  },

  created() {
    if (this.listenEvent) {
      this.$events.listen(this.listenEvent, filters => {
        this.filters = filters
        this.fetch()
      })
    }
    this.fetch()
  },

  methods: {
    async fetch() {
      this.ready = false
      const url = this.makeUrl()
      const resp = await this.$api.get(url)
      if (resp.fail) {
        if (!resp.status) {
          return this.$notifyError(resp)
        }
      }
      this.previous = this.block
      this.block = resp.data
      this.$nextTick(() => (this.ready = true))
    },

    makeUrl() {
      if (this.filters.length) {
        return this.url + '?' + buildUrl(this.filters)
      }

      if (this.request) {
        return this.url + '?' + buildUrl(this.request)
      }
      return this.url
    },

    reload() {
      this.fetch()
    },
  },

  render(c) {
    if (!this.ready) {
      return null
    }

    const block = this.block

    const loader = this

    const props = {
      ...block.props,
      reload: loader.reload,
      tab: this.tab,
      ...(this.subprops || {}),
    }
    return c(block.component, {
      tag: 'component',
      props: props,
      on: {
        'page-title': title => {
          this.$emit('page-title', title)
        },
      },
    })
  },

}
</script>
