import Service from '../../lib/services/Service'
import detectorMaker from 'element-resize-detector'
import Vue from 'vue'

const stub = {
  tabsActive: false,
  currentSize: {
    width: null,
    height: null,
  },
  fullscreen: false,
  sidebar: {
    width: 240,
    fixed: true,
    hover: false,
  },
}

export default class LayoutManager extends Service {
  get sidebarCollapsed() {
    if (!this.sidebar.fixed && this.sidebar.hover) {
      return false
    }
    return this.$state.currentSize.width < 769 || !this.sidebar.fixed
  }

  get sidebar() {
    return this.$state.sidebar
  }

  toggleSidebar() {
    this.$state.sidebar.fixed = !this.$state.sidebar.fixed
  }

  init() {
    this.$state = Vue.observable({ ...stub })
    return super.init()
  }

  bindLayout(layoutEl) {
    this.erd = detectorMaker()
    this.erd.listenTo(layoutEl, el => {
      this.$state.currentSize.width = el.offsetWidth
      this.$state.currentSize.height = el.offsetHeight
    })
  }
}
