import Parser from 'route-parser'
import Service from '../services/Service'
import Route from './route'
import { removeTrailingSlash } from '../utils'

export default class Router extends Service {
  /**
   * Debug to console (or not)
   *
   * @type {boolean}
   */
  $debug = false

  /**
   * Defined application routes
   *
   * @type {Array}
   */
  $routes = []

  /**
   * Current active route
   *
   * @type {null}
   */
  currentRoute = null

  /**
   * Current matched route
   * (not active yet)
   *
   * @type {null}
   */
  matchedRoute = null

  /**
   * Window history listeners
   *
   * @type {Array}
   */
  listeners = []

  /**
   * Route change listeners
   *
   * @type {Object}
   */
  routeListeners = {}

  /**
   * Base url for the route paths
   *
   * @type {string}
   */
  baseUrl = '/'

  clickEvent = null

  init() {
    this.listenClicks()
    this.listenHistory()

    this.setBaseUrl()

    return this
  }

  addRouteListener(key, callback) {
    this.routeListeners[key] = callback
  }

  removeRouteListener(key) {
    delete this.routeListeners[key]
  }

  setBaseUrl() {
    if (!this.superv.config.baseUrl) {
      return
    }
    let url = removeTrailingSlash(this.superv.config.baseUrl)

    // make sure path starts with /
    if (url[0] !== '/') {
      url = '/' + url
    }

    this.baseUrl = url
  }

  boot() {
    this.hydrateRoutes()
    this.loadState()
    this.debug('Base Url is: ' + this.baseUrl)
  }

  loadState() {
    this.activateRoute(this.match(this.normalizePath(window.location.pathname)))
  }

  get route() {
    return this.currentRoute
  }

  changeRoute(newPath, state) {
    this.activateRoute(this.match(newPath)) || window.history.go(-1)
  }

  back() {
    window.history.go(-1)
  }

  push(path, state, event = null) {
    this.clickEvent = event

    this.debug('Pushing ' + path)

    if (path.startsWith('http')) {
      if (window.parent) {
        return window.parent.location.replace(path)
      }
      return window.location.replace(path)
    }

    const route = this.match(this.normalizePath(path))
    if (route) {
      this.pushState(route.resolvedPath, state)
    } else {
      console.log(path, this.normalizePath(path))
      this.notFound(path)
    }
  }

  pushState(url, state) {
    if (!url.startsWith('/')) {
      url = '/' + url
    }
    window.history.pushState(state, null, url)
    this.listeners.forEach(listener =>
      listener(url, state, window.location.pathname),
    )
  }

  normalizePath(path) {
    if (!path) {
      return
    }
    if (this.baseUrl !== '/' && !path.startsWith(this.baseUrl)) {
      path = this.baseUrl + (path.startsWith('/') ? path : '/' + path)
    }

    // make sure path starts with /
    if (path[0] !== '/') {
      path = '/' + path
    }

    return removeTrailingSlash(path)
  }

  activateRoute(route) {
    if (!route) {
      return
    }

    if (this.clickEvent) {
      route.clickEvent = this.clickEvent
      this.clickEvent = null
    }
    route.build()
    this.debug('Activating route: ' + route.resolvedPath)
    if (route.isRedirect) {
      this.debug('redirecting to: ' + route.redirect)
      return this.push(route.redirect)
    }

    if (!route.layout) {
      route.layout = { name: 'default' }
    }

    const layout = this.layouts.find(
      _layout => _layout.name === route.layout.name,
    )

    route.layout = { ...layout, ...route.layout }

    if (route.layout.before) {
      // this.debug('checking layout before')
      if (!route.layout.before(this.superv)) {
        this.debug('layout before failed')
        this.$events.fire('route:layout-before-failed', route)
        return
      }
    }

    if (route.before) {
      // this.debug('checking route before')
      if (!route.before(this.superv)) {
        this.debug('route before failed')
        this.$events.fire('route:route-before-failed', route)
        return
      }
    }

    this.$events.fire('route:matched', route)
    this.$events.fire('route:changing', route)
    this.currentRoute = route
    this.$events.fire('route:changed', route)

    Object.values(this.routeListeners).map(callback => callback.apply())

    return true
  }

  notFound(path) {
    this.superv.$notifyError('Path not found: ' + path)
  }

  listenHistory() {
    this.listeners.push((newPath, state, currentPath) => {
      this.changeRoute(newPath, state)
    })

    window.addEventListener('popstate', () => this.loadState())
  }

  setLayouts(layouts) {
    this.layouts = layouts
  }

  addRoutes(routes) {
    if (!routes || !routes.length) {
      return
    }
    this.$routes = [...this.$routes, ...routes]

    return this
  }

  hydrateRoutes() {
    this.$routes.map(route => {
      route.path = this.normalizePath(route.path)

      if (route.meta) {
        if (route.meta.layout) {
          let layout = route.meta.layout
          if (typeof layout === 'string') {
            route.meta.layout = { name: layout }
          }
        }
      }
    })
  }

  removeTrailingSlash(string) {
    if (string === '/') {
      return string
    }
    return string.replace(new RegExp('/' + '+$'), '')
  }

  home() {
    this.push(this.baseUrl)
  }

  match(path) {
    path = this.normalizePath(path)
    this.matchedRoute = null
    this.debug('LOOKING FOR: ' + path)
    this.$routes.find(route => {
      let params = new Parser(route.path).match(path)
      this.debug('CHECKING: ' + route.path, params)
      if (params) {
        return (this.matchedRoute = this.hydrate(route, params, path))
      }
    })

    this.debug('MATCH RESULT: ', this.matchedRoute)
    return this.matchedRoute
  }

  goBack() {
    window.history.length > 1 ? window.history.go(-1) : this.push('/')
  }

  listenClicks() {
    document.addEventListener('click', event => {
      const elements = document.querySelectorAll('a:not(.external-link)')

      if (elements) {
        let el = event.target
        let index
        while (
          el &&
          (index = Array.prototype.indexOf.call(elements, el)) === -1
          ) {
          el = el.parentElement
        }

        if (index > -1) {
          const href = el.getAttribute('href')
          if (href) {
            this.push(href, null, event)
            event.preventDefault()
          }
        }
      }
    })
  }

  parseQuery() {
    let params = {}
    window.location.search
    .replace('?', '')
    .split('&')
    .map(raw => {
      let key, value
      ;[key, value] = raw.split('=')
      params[key] = value
    })
    return params
  }

  hydrate(routeData, params, path) {
    const route = new Route(routeData)
    route.params = params
    route.resolvedPath = path
    // route.build()

    return route
  }
}
