<template>
  <div>
    <button @click.prevent="click" class="sv-btn success sm" :class="'disabled' ? showModal : ''">
      {{ title }}
    </button>
    <sv-modal v-if="showModal" :visible.sync="showModal" @close="complete">
      <template slot="body" slot-scope="{close}">
        <div class="px-8 py-10 ui form">

          <sv-form-fields
              v-if="pivotFields"
              :fields="pivotFields"
              :form="form"
          >
          </sv-form-fields>
        </div>
        <sv-table
            :config="tableConfig"
        >
        </sv-table>
      </template>
    </sv-modal>
  </div>
</template>

<script>

import SvModal from './SvModal'
import SvTable from './table/SvTable'
import SvFormFields from './form/SvFormFields'
import FormModel from './form/FormModel'

export default {
  name: 'SvAttachEntryAction',
  components: { SvFormFields, SvTable, SvModal },
  props: ['title', 'id', 'lookup-url', 'attach-url', 'table', 'pivot-fields'],

  data() {
    return {
      showModal: false,
      tableConfig: null,
      form: null
    }
  },

  created() {
    this.form = new FormModel({ url: '', method: '', fields: [] }, this.$superv)
  },

  methods: {
    click() {
      this.fetchTable()
    },
    selected(selected) {
      this.selected = selected
    },
    async fetchTable() {
      this.showModal = false
      const resp = await this.$api.get(this.lookupUrl)
      if (resp.fail) {
        return this.$notifyError(resp)
      }
      this.tableConfig = resp.data.props.config

      this.tableConfig.selection_actions = [{
        component: 'sv-attach-selected-action',
        props: {
          url: this.attachUrl,
          formData: this.form.data,
          onComplete: () => this.complete()
        }
      }]

      // override default row actions
      this.tableConfig.row_actions = [{
        component: 'sv-select-row-action',
        props: {
          url: this.attachUrl,
          formData: this.form.data,
          onComplete: (closeModal) => this.complete(closeModal)
        }
      }]
      this.$nextTick(() => this.showModal = true)

    },
    closeModal() {
      this.showModal = false
    },
    complete(closeModal = true) {
      this.$notifySuccess('ok')
      if (closeModal) {
        // this.closeModal()
      }

      // reload "parent" table
      this.table.reload()
    }
  },
}
</script>

<style scoped>
  .disabled {
    opacity: 0.25;
    pointer-events: none;
  }

  .sv-btn {
    margin-right: 1rem;
  }
</style>
