import Button from '../../lib/models/Button'

export default class FormModel {
  data = {}

  parent = null

  errors = {}

  childForms = {}

  callbacks = {}

  response = null

  constructor({ identifier, url, method, fields, actions }, superv, $set) {
    this.identifier = identifier
    this.url = url
    this.method = method
    this.fields = fields || []
    this.actions = actions || []
    this.superv = superv
    this.$set = $set
    this.$api = superv.$api
  }

  get defaultAction() {
    let action = this.actions.find(action => action.default === true)
    if (!action) {
      action = { title: 'Submit' }
    }

    return this.makeActionButton(action)
  }

  get subActions() {
    return this.actions
    .filter(action => action.default !== true)
    .map(action => this.makeActionButton(action))
  }

  async init() {
    if (this.fields.length === 0) {
      const resp = await this.$api.get(this.url)


      this.identifier = resp.data.props.identifier
      this.url = resp.data.props.url
      this.fields = resp.data.props.fields || []
    }
    this.buildFieldData()

    return this
  }

  initParent(parent) {
    this.parent = parent

    parent.childForms[this.identifier] = this
    this.$set(parent.data, this.identifier, this.data)

    return this
  }

  fill(data) {
    Object.keys(data).map(key => {
      // this.fields[key].value = data[key]
      this.setFieldValue(key, data[key])
      // this.$set(this.getField(key), 'value', data[key])
    })
  }

  buildFieldData() {
    this.fields.map(field => this.$set(this.data, field.name, field.value))
  }

  makeActionButton(action) {
    return Button.make(action).onClick(() => this.submit(action))
  }

  async submit(action) {
    const formData = this.formData()
    if (action.identifier) {
      formData.append('__form_action', action.identifier)
    }
    const resp = await this.superv.$api.post(this.url, formData)

    this.parseErrorsFromResponse(resp)

    if (resp.fail) {
      if (resp.status === 422) {
        return resp
      } else {
        if (this.callbacks.error) {
          return this.callbacks.error.apply()
        }
        return this.superv.$notifyError(resp)
      }
    }

    this.response = resp.$data

    if (resp.$data.events) {
      resp.$data.events.map(e => {
        this.superv.$events.fire(e)
      })
    }

    if (this.callbacks.success) {
      this.callbacks.success.apply()
    } else {
      this.superv.$notifySuccess(resp.data.message)
      if (resp.data.redirect_to) {
        this.superv.$router.push(resp.data.redirect_to)
      }
    }

    return resp
  }

  formData(formData) {
    if (!formData) {
      formData = new FormData()
    }

    this.fields.map(field => {
      let value = this.getFieldValue(field.name)

      if (
        field.hidden === true ||
        typeof value === 'undefined' ||
        value === null
      ) {
        return
      }

      if (value instanceof FileList) {
        formData.append(field.name, value[0], value[0].name)
      } else {
        if (value.constructor === Object || value.constructor === Array) {
          formData.append(field.name, JSON.stringify(value))
        } else {
          formData.append(field.name, value)
        }

        // if (value.constructor === Array) {
        //   // console.log(field)
        //   // formData.append(field.name + '[]', value)
        // }
      }
    })

    Object.keys(this.childForms).map(identifier => {
      const childForm = this.childForms[identifier]
      const childFormData = childForm.formData()

      childForm.fields.map(field => {
        let keyName = [identifier, '[', field.name, ']'].join('')
        formData.append(keyName, childFormData.get(field.name))
      })
    })

    return formData
  }

  getField(name) {
    return this.fields.find(field => field.name === name)
  }

  getFieldValue(name) {
    return this.data[name]
  }

  setFieldValue(name, value) {
    const field = this.getField(name)
    if (field) {
      field.value = value
    } else {
      console.log('field not found: ' + name)
    }
  }

  hasError(field) {
    return !!this.getError(field)
  }

  getError(field) {
    const errors = this.errors[field.name]

    return errors && errors[0]
  }

  parseErrorsFromResponse(resp) {
    let errors = {}

    // Ugly belongs_to column name fix
    //
    if (resp.$data.errors) {
      Object.keys(resp.$data.errors).map(key => {
        const k = key.replace(new RegExp('_id' + '$'), '')
        errors[k] = resp.$data.errors[key]
      })
    }
    this.errors = errors
  }

  onSuccess(callback) {
    this.callbacks.success = callback
    return this
  }

  onError(callback) {
    this.callbacks.error = callback
    return this
  }
}
