<template>
  <div class="sv-stacked-router pt-8">
    <div class="sv-router-tabs">
      <a @click.prevent="closeAll" class="btn-close mr-1 w-3 btn-tab-hover:text-gray-500 text-transparent text-base"
         href="">&times;</a>
      <div :class="[tab.active ? 'active' : '', tab.pinned ? 'pinned' : '']" class="inline-flex justify-between btn-tab"
           v-for="tab in tabs">
        <button
            @click.prevent="activateTab(tab)"
            class="btn-tab-hover:title"
        >
          {{ tab.title || 'Tab # ' + tab.id }}
        </button>
        <a @click.prevent="removeTab(tab)" class="btn-close mr-1 w-3 btn-tab-hover:text-gray-500 text-transparent text-base"
           href="">&times;</a>
      </div>
    </div>
    <div class="flex-grow flex flex-col">
      <transition-group
          :class="[preview ? 'xlider-list-preview' : 'xlider-list', slideDirection]"
          class="overflow-y-auto"
          name="xlider"
          tag="div"
      >
        <div :class="{'xlider-preview': preview}"
             :key="tab.id"
             v-for="tab in tabs"
             v-show="tab.active || preview">
          <component
              :is="tab.component"
              @title="title => tab.title = title"
              v-bind="tab.props"
          ></component>
        </div>
      </transition-group>
    </div>
  </div>
</template>
<script>
export default {
  name: 'SvStackedRouter',
  props: ['id'],

  data() {
    return {
      tabs: [],
      current: {
        component: null,
        props: {}
      },
      loaded: null,
      slideDirection: 'to-left',
      preview: false
    }
  },

  computed: {
    activeTab() {
      return this.tabs.find(tab => tab.active === true)
    },
    firstTab() {
      return this.tabs[0]
    }
  },

  created() {
    this.$events.listen('route:changed', (newRoute) => this.routeChanged(newRoute))
    this.load()
    if (this.$route) {
      this.activateRoute(this.$route)

    } else {
      alert('you should not be seeing this sticky')
    }

    // in case we fail to activate any tabs
    // activate the first from stack
    this.$nextTick(() => {
      if (this.tabs.length && !this.activeTab) {
        this.$notifyError('happend')
        this.firstTab.active = true
      }
    })

  },

  methods: {
    routeChanged(newRoute) {
      this.$debug('route changed')
      this.activateRoute(newRoute)
    },
    activateRoute(route) {
      this.activeTab && (this.activeTab.active = false)
      this.newTab(route)
    },
    activateTab(tab) {
      if (this.activeTab) {
        if (tab.id > this.activeTab.id) {
          this.slideDirection = 'to-left'
        } else {
          this.slideDirection = 'to-right'
        }
        this.activeTab.active = false
      }

      tab.active = true
      this.save()
    },
    newTab(route) {
      const tab = {
        id: this.getTabId(),
        title: '',
        props: { ...route.props },
        component: route.component,
        pinned: false,
        active: true,
      }
      this.activeTab && (this.activeTab.active = false)

      this.tabs.push(tab)
      this.save()
    },
    removeTab(tab) {
      // if it's the only tab we have,
      // unpin instead of removing
      if (this.tabs.length === 1) {
        tab.pinned = false
        return
      }
      const index = this.tabs.indexOf(tab)
      if (index > -1) {
        this.tabs.splice(index, 1)
      }
      if (this.tabs.length > 0) {
        // if active is removed, activate first from stack
        if (tab.active) {
          this.$nextTick(() => this.firstTab.active = true)
        }
      }
      this.save()
    },
    closeAll() {
      this.tabs = []
      this.save()
    },
    save() {
      localStorage.setItem(this.$sv.config.name + ':tabs', JSON.stringify(this.tabs))
    },
    load() {
      const tabs = localStorage.getItem(this.$sv.config.name + ':tabs')
      if (tabs) {
        this.tabs = this.loaded = JSON.parse(tabs)
      }
    },
    getTabId() {
      if (this.tabs.length === 0) {
        return 1
      }

      const tabIds = this.tabs.filter(tab => tab.id !== null).map(tab => tab.id)

      return Math.max(...tabIds) + 1
    }
  },
}
</script>
