<template>
  <div
      :class="{'sb-fixed': sidebar.fixed, 'sb-collapsed':collapsed}"
      @mouseenter="mouseOver"

      :style="{'width': !collapsed ?  (sidebar.width + 'px') : null, 'min-width': !collapsed ?  (sidebar.width + 'px') : null}"
      @mouseleave="mouseOut"
      class="sv-sidebar-wrapper hidden sm:flex    flex-col bg-bg"
  >
    <aside @dblclick="fetch" class="sv-sidebar">
      <div v-if="!ready">
        <sv-loading-block></sv-loading-block>
      </div>

      <div :class="{'sb-collapsed': this.collapsed}" class="sv-nav"
           v-else>
        <ul class="sv-list" style="transition:all 1s ease;">
          <template v-for="module in modules">
            <li class="package-header">
              <i class="text-xs zmdi zmdi-ungroup fill-current mr-1"></i>
              <span class="break-all">{{  module.title  }}</span>
            </li>
            <sv-sidebar-section
                :goTo="goTo"
                :key="section.handle"
                :section="section"
                :toggleSection="toggleSection"
                v-for="section in module.sections"
            ></sv-sidebar-section>

          </template>
        </ul>
      </div>

    </aside>


  </div>
</template>

<script>
import 'perfect-scrollbar/css/perfect-scrollbar.css'
import PerfectScrollbar from 'perfect-scrollbar'

import BaseNavigation from './BaseNavigation'

import SvLoadingBlock from '../SvLoadingBlock'
import SvSidebarSection from './SvSidebarSection'
import SvButton from '../SvButton'
import SvIcon from '../SvIcon'

export default {
  name: 'SvSidebar',
  mixins: [BaseNavigation],
  components: {
    SvIcon,
    SvButton,
    SvSidebarSection,
    SvLoadingBlock,
  },

  computed: {
    collapsed() {
      return this.$sv.$layout.sidebarCollapsed
    },
    modules() {
      return Object.values(this.nav.sections)
    },

    sidebar() {
      return this.$sv.$layout.sidebar
    },

    cacheKey() {
      return this.$sv.config.name + ':sidebar.fixed'
    },
  },

  watch: {
    nav: function() {
      this.$nextTick(() => this.initScroll())
    },
    'sidebar.fixed': function () {
      this.$storage.set('sidebar.fixed', this.sidebar.fixed)
    },
  },

  created() {
    this.sidebar.fixed = this.$storage.get('sidebar.fixed', this.sidebar.fixed)
  },

  methods: {
    mouseOver() {
      if (this.sidebar.fixed || this.sidebar.hover) {
        return
      }
      this.sidebar.hover = true
    },
    mouseOut() {
      if (this.sidebar.fixed || !this.sidebar.hover) {
        return
      }
      this.sidebar.hover = false
    },
    toggleSection(section) {
      this.$set(section, 'open', !section.open)
      this.updateScroll()
    },

    goTo(section, page, event) {
      this.modules.map(module => {
        Object.values(module.sections).map(section$ => {
          this.$set(section$, 'open', false)
          this.$set(section$, 'active', false)
        })
      })
      this.$router.push(page ? page.url : section.url, null, event)
      this.setCurrentSection(section)
    },

    initScroll() {
      if (!this.ps && this.ready) {
        const container = document.querySelector('.sv-list')
        this.ps = new PerfectScrollbar(container)
      }
    },

    updateScroll() {
      this.ps.update()
    },

    setCurrentSection(section) {
      this.$set(section, 'active', true)
      localStorage.setItem(this.$sv.config.name + ':section', section)
    },
  },
}
</script>

