<template>
  <button @click.prevent="select" class="sv-btn success sm">
    Select
  </button>
</template>

<script>
export default {
  name: 'SvSelectRowAction',
  props: ['url', 'table', 'row-id', 'on-complete', 'form-data'],
  methods: {
    async select() {
      const resp = await this.$api.post(this.url, { items: [this.rowId], form_data: this.formData })
      if (resp.fail) {
        return this.$notifyError(resp)
      }

      await this.table.reload()


      // close modal if no rows left to attach
      this.onComplete(this.table.rows.length === 0)
    }
  },
}
</script>
