<template>
  <div class="sv-form-wrapper px-0 sm:px-4" v-if="ready">
    <div class="sv-form bg-white">
      <div class="card-header" v-if="title"><h2>{{ title }}</h2></div>

      <div class="sv-form-group-wrapper" v-for="field in form.fields">

        <div class="w-full" v-if="field.meta && field.meta.full">
          <sv-field
              :field="field"
              :form="form"
              v-model="field.presenting ? field.value : form.data[field.name]"
          >
          </sv-field>
          <p class="sv-field-helper">{{ form.hasError(field) ? form.getError(field): ''}}</p>
        </div>

        <div :class="{'error': form.hasError(field)}" class="sv-form-group justify-center sm:flex-row" v-else>
          <p class="field-label hidden xs:block sm:w-1/3 max-w-md text-left p-1">{{ field.label }}</p>
          <div class="field-input w-full sm:w-2/3 max-w-2xl">
            <sv-field
                :field="field"
                :form="form"
                v-model="field.presenting ? field.value : form.data[field.name]"
            >
            </sv-field>
            <p class="sv-field-helper" v-if="form.hasError(field)">{{ form.hasError(field) ? form.getError(field):
              ''}}</p>
            <p class="mt-4 text-sm" style="color: #979ca8;" v-if="field.hint">{{ field.hint }}</p>
          </div>
        </div>
      </div>

      <slot :form="form" name="fields"></slot>

      <div class="sv-form-footer">

        <div class="form--actions flex items-center justify-end" v-if="!form.parent">

          <div class="form--sub-actions flex flex-row-reverse">
            <div class="ml-2" v-for="(button, key) in form.subActions">
              <sv-button :button="button" :style="{ opacity: 1 - ((key + 1) * .3)}" class="green"></sv-button>
            </div>
          </div>

          <div class="ml-8">
            <sv-button :button="form.defaultAction"></sv-button>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvButton from '../SvButton'
import FormModel from './FormModel'
import SvFormFields from './SvFormFields'
import SvBooleanField from './fields/SvBooleanField'

export default {
  name: 'SvForm',
  components: { SvBooleanField, SvFormFields, SvButton },
  props: ['identifier', 'title', 'url', 'method', 'fields', 'actions'],

  data() {
    return {
      form: {},
      ready: false,
    }
  },

  created() {
    this.form = new FormModel(this.$props, this.$superv, this.$set)

    if (this.$parent.form) {
      this.form.initParent(this.$parent.form)
    }
    this.form.init()

    this.ready = true
    this.$emit('form', this.form)
  },
}
</script>

<style>
  .form--sub-actions {
    opacity: 0;
    visibility: hidden;
    -webkit-transition: opacity 600ms, visibility 600ms;
    transition: opacity 600ms, visibility 600ms;
  }

  .form--actions:hover > .form--sub-actions {
    visibility: visible;
    opacity: 1;
  }

  .sv-form-footer {
    padding: 25px;
    border-top: 1px solid #ebedf2;
    box-sizing: border-box;
  }
</style>
