</template>

<script>
import SvButton from '../SvButton'
import FormModel from './FormModel'
import SvForm from './SvForm'

export default {
  name: 'SvMultiForm',
  components: { SvForm, SvButton },
  props: ['identifier', 'title', 'url', 'method', 'blocks', 'actions'],

  data() {
    return {
      form: {},
      ready: false,
    }
  },

  created() {
    const state = {
      url: this.url,
      method: this.method,
      fields: this.fields,
      actions: this.actions,
    }
    this.form = new FormModel(state, this.$superv, this.$set)
    this.form.init()

    this.ready = true
  },

  methods: {
    props(block) {
